.custom-radio {
    display: block;
    position: relative;
    margin-bottom: 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: inline-block;
    border-color: transparent;
    border-width: $input-border-width;
    border-style: solid;
    color: $input-color;
    min-height: $input-height;
    padding: 12px 20px;
    line-height: $input-line-height;
    margin-right: 0.8rem;
    margin-top: 5px;
    &:last-child {
        margin-right: 0;
    }
    &.custom-radio-disabled {
        opacity: 0.6;
        pointer-events: none;
    }
    @include media-breakpoint-up(xxxl) {
        padding: 20px 30px;
    }
    input {
        position: absolute;
        opacity: 0 !important;
        cursor: pointer;
        height: 0;
        width: 0;
        visibility: hidden;
    }
    .checkmark {
        position: absolute;
        top: 0;
        right: 0;
        height: 24px;
        width: 24px;
        background-color: #eee;
        border-radius: 50%;
        margin-top: -6px;
        margin-right: -6px;
        z-index: 2;
        opacity: 0;
    }
    &:hover input~.checkmark {
        background-color: #ccc;
    }
    input:checked~.checkmark {
        background-color: var(--primary-color);
        opacity: 1;
    }
    .checkmark:after {
        content: "";
        position: absolute;
        display: none;
    }
    input:checked~.checkmark:after {
        display: block;
    }
    .checkmark:after {
        left: 8px;
        top: 4px;
        width: 6px;
        height: 12px;
        border: solid #fff;
        border-width: 0 2px 2px 0;
        transform: rotate( 45deg);
        content: "";
        position: absolute;
    }
    .box-line:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-color: #E0E1E6;
        border-width: $input-border-width;
        border-style: solid;
        border-radius: 10px;
        @include media-breakpoint-up(xxxl) {
            border-radius: 15px;
        }
        &:focus {
            box-shadow: $input-focus-box-shadow;
        }
    }
    input:checked~.box-line:before {
        border-color: var(--primary-color);
    }
    &.disabled{
        cursor: not-allowed;
        background: $grey-1100;
        border-radius: 10px;
    }
}

.custom-checkbox {
    display: block;
    position: relative;
    margin-bottom: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: inline-block;
    border-color: transparent;
    border-width: $input-border-width;
    border-style: solid;
    color: $input-color;
    min-height: initial;
    padding: 0 15px 0 30px;
    line-height: $input-line-height;
    margin-right: 0.8rem;
    &:last-child {
        margin-right: 0;
    }
    input {
        position: absolute;
        opacity: 0 !important;
        cursor: pointer;
        height: 0;
        width: 0;
    }
    .checkmark {
        position: absolute;
        top: 50%;
        left: 0;
        height: 16px;
        width: 16px;
        background-color: $white;
        border:1px solid $gray-500;
        margin-top: -8px;
        border-radius: 2px;
    }
    &:hover input~.checkmark {
        background-color: #ccc;
    }
    input:checked~.checkmark {
        background-color: var(--primary-color);
        border-color: var(--primary-color);
    }
    .checkmark:after {
        content: "";
        position: absolute;
        display: none;
    }
    input:checked~.checkmark:after {
        display: block;
    }
    .checkmark:after {
        left: 5px;
        top: 1px;
        width: 5px;
        height: 10px;
        border: solid white;
        border-width: 0 2px 2px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        @include media-breakpoint-up(xxxl){
            top: 1px;
        }
    }
    .checkbox-line:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-color: $input-border-color;
        border-width: $input-border-width;
        border-style: solid;
        border-radius: $input-border-radius;
        &:focus {
            box-shadow: $input-focus-box-shadow;
        }
    }
    input:checked~.checkbox-line:before {
        border-color: $input-focus-border-color;
    }
    &.custom-checkbox-r-border {
        padding: 0 0 0 ($input-padding-x + 0.75rem);
        min-height: auto;
        .checkmark {
            top: 2px;
            left: 0;
        }
        .checkbox-line:before {
            border: none;
        }
    }
    &.no-label {
        @include media-breakpoint-up(lg) {
            margin-top: 38px;
        }
    }
    &.disabled{
        cursor: not-allowed;
    }
    input:disabled ~ .checkmark{
        background: $grey-1100;
        border-color: $grey-1100;
    }
    input:checked:disabled ~ .checkmark{
        background: $primary;
        border-color: $primary;
        opacity: 0.8;
    }
}

