.ingriadiant-make-editable {
    tbody {
        tr {
            td:nth-child(1) {
                width: 100%;
            }
            td:last-child{
                vertical-align: top;
                padding-top: 12px;
            }
        }
        tr:not(:last-child) {
            td {
                .ingriadiant-remove {
                    display: block;
                }
                #ingriadiantAdd {
                    display: none;
                }
            }
        }
        tr:last-child {
            td {
                .ingriadiant-remove {
                    display: none;
                }
            }
        }
    }
}

.nutrition-make-editable {
    tbody {
        tr {
            td:nth-child(1) {
                width: 50%;
            }
            td:nth-child(2) {
                width: 50%;
            }
        }
        tr:not(:last-child) {
            td {
                .nutritions-remove {
                    display: block;
                }
                .nutritions-add {
                    display: none;
                }
            }
        }
        tr:last-child {
            td {
                .nutritions-remove {
                    display: none;
                }
            }
        }
    }
}

