// drag and drop 
body.dragging, body.dragging * { cursor: move !important; }
.draggable-outer{padding: 15px;border: 1px solid #eee;border-radius: 10px;overflow: hidden;}
.draggable-outer:hover {border-color: var(--primary-color);}
.dragged { position: absolute; opacity: 0.5; z-index: 2000; }
.draggable { margin: 0 0 9px 0; min-height: 10px; height: 250px; list-style-type: none; padding: 0px; overflow-x: hidden; overflow-y: auto;}
.draggable li { padding: 5px; margin-top: 5px;  }
.draggable li i{opacity: 0.3;  }
.draggable li:hover { cursor: move !important;background: #f2fbfd; }
.draggable li:hover i{opacity: 0.5;}
.draggable li.placeholder { position: relative; margin: 0; padding: 0; border: none; }
.draggable li.placeholder:before { position: absolute; content: ""; width: 0; height: 0; margin-top: -8px; left: 10px; top: 17px; border: 5px solid transparent; border-left-color: var(--primary-color); border-right: none; margin-left: -10px; opacity: 1;}
.draggable-outer .name{background: #f5f5f5;padding: 5px;margin: -15px -15px 20px;font-size: 15px;}
.draggable-outer:hover .name{color: var(--primary-color);}
#teamAssignmentFrm .department-box .department-box-select {background: #f5f5f5;color:#212529;}
.draggable-outer.limit-error, .draggable-outer.limit-error:hover { border-color: var(--danger) !important; }
.draggable-outer.limit-error .name, .draggable-outer.limit-error:hover .name { color: var(--danger) !important; }
.draggable-outer.limit-error .count-wrapper, .draggable-outer.limit-error:hover .count-wrapper { color: var(--danger) !important; }
.draggable-outer.limit-error .count, .draggable-outer.limit-error:hover .count { color: var(--danger) !important; }


