/*
 * Page: Login & Register
 * ----------------------
 */

.login-logo,
.register-logo {
  font-size: 35px;
  text-align: center;
  margin-bottom: 25px;
  font-weight: 300;

  a {
    color: #444;
  }
}

.login-page,
.register-page {
  background: $gray-200;
}

.login-box,
.register-box {
  width: 90%;
  margin-top: 20px;
  @include media-breakpoint-up(sm){
    width: 360px;
    margin: 7% auto;
  }
}

.login-box-body,
.register-box-body {
  background: $white;
  padding: 20px;
  border-top: 0;
  color: #666;

  .form-control-feedback {
    color: #777;
  }
}

.login-box-msg,
.register-box-msg {
  margin: 0;
  text-align: center;
  padding: 0 20px 20px 20px;
}

.social-auth-links {
  margin: 10px 0;
}

/********************
 New 
 ********************/
.non-auth-area {
  display: flex;
  min-height: 100vh;
}

.non-auth-left-area {
  position: relative;
  width: calc(100% - 406px);
  width: -webkit-calc(100% - 406px);
  background-color: var(--primary-color);
  // background-image: url('../img/web-login.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: none;
  &:before{
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    bottom :0;
    right: 0;
    background-color: var(--primary-color);
    opacity: 0.9;
    z-index: 1;
  }

  @include media-breakpoint-up(md) {
    display: block;
  }
}

.auth-slider {
  height: 100%;
  overflow: hidden;
  
  .owl-stage-outer,
  .owl-stage,
  .owl-item,
  .item {
    height: 100%;
  }

  .owl-dots {
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    text-align: center;

    .owl-dot {
      height: 10px;
      width: 10px;
      background: #1177A3;
      border-radius: 20px;
      margin: 0 3px;
      outline: none;
      transition: all 0.7s;

      &.active {
        background: #ffffff;
      }
    }
  }
}
.carousel-image{
  max-height: 100vh;
  display: block;
  margin-left: auto;
}
.auth-slid {
  position: relative;
  z-index: 2;
  min-height:100vh;
  .worklife-login-logo{
    max-width: 240px;
    @include media-breakpoint-up(xxxl) {
      max-width: 300px;
    }
  }
  .auth-slid-text-area {
    max-width: 600px;
    width: 90%;
    margin: 0 auto;
    text-align: center;
    h1{
      font-size: 24px;
      line-height: 1.4;
      padding-bottom: 20px;
      border-bottom: 1px solid $white;
      margin-bottom: 20px;
      @include media-breakpoint-up(xxxl) {
        font-size: 26px;
      }
    }
    .sub-header{
      font-size: 16px;
      line-height: 1.4;
      margin-bottom: 30px;
      color: $gray-200;
      @include media-breakpoint-up(xxxl) {
        font-size: 18px;
      }
    }
    p{
      font-size: 13px;
      line-height: 1.5;
      color: $gray-200;
      @include media-breakpoint-up(xxxl) {
        font-size: 14px;
      }
    }
  }
}

.non-auth-right-area {
  width: 100%;
  min-width: auto;
  max-width: 100%;
  background: $white;
  @include media-breakpoint-up(md) {
    width: 406px;
    min-width: 406px;
    max-width: 406px;
  }

  .non-auth-right-head {
    .non-auth-logo {
      height: 64px;
      margin: 30px auto;
      display: block;
      @include media-breakpoint-up(lg) {
        height: 74px;
      }
      @include media-breakpoint-up(xxxl) {
        height: 84px;
      }
    }

    .non-auth-logo-text {
      font-size: 16px;
      margin-left: 8px;

      span {
        color: var(--primary-color);
      }
    }
  }
}

.form-group-bottom {
  .form-control {
    border-top-color: transparent;
    border-right-color: transparent;
    border-left-color: transparent;
    border-radius: 0;

    &:focus {
      border-top-color: transparent;
      border-right-color: transparent;
      border-left-color: transparent;
      box-shadow: none;
    }
  }
}


.btn-microsoft-img {
  max-width: 19px;
  margin-right: 10px;
  width: 19px;
}

//cookie
.modal-backdrop {
  background: rgba(0, 0, 0, 0.7);
}

.agree-modal {
  height: 100vh;

  .content-wrapper,
  .header-main,
  .footer,
  section,
  &>div:not(.cookieModal) {
    filter: blur(4px);
    -webkit-filter: blur(4px);
    -moz-filter: blur(4px);
  }
}