/*
 * Component: Table
 * ----------------
 */

.table.no-border {
    &,
    td,
    th {
        border: 0;
    }
}

// .text-center in tables
.table.text-center {
    &,
    td,
    th {
        text-align: center;
    }
}

.table-valign-middle {
    thead>tr>th,
    thead>tr>td,
    tbody>tr>th,
    tbody>tr>td {
        vertical-align: middle;
    }
}

.card-body.p-0 .table {
    thead>tr>th,
    thead>tr>td,
    tbody>tr>th,
    tbody>tr>td {
        &:first-of-type {
            @extend .ps-4;
        }
        &:last-of-type {
            @extend .pe-4;
        }
    }
}

table.dataTable td,
.table td {
    vertical-align: middle;
}

.th-btn-sm {
    min-width: 80px;
    max-width: 80px;
    width: 80px;
}

.th-btn-2 {
    min-width: 140px;
    max-width: 140px;
    width: 140px;
}

.th-btn-3 {
    min-width: 127px;
    max-width: 127px;
    width: 127px;
}

.th-btn-4 {
    min-width: 162px;
    max-width: 162px;
    width: 162px;
}

.th-btn-5 {
    min-width: 192px;
    max-width: 192px;
    width: 192px;
}

.th-btn-6 {
    min-width: 260px;
    max-width: 260px;
    width: 260px;
}

.tbl-user-img {
    width: 50px;
    height: 50px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.table td .btn-icon {
    margin: 2px 0px;
}

.table .tabl-img {
    width: 50px;
}

.table-hover tbody tr:hover img.tbl-user-img {
    border-radius: 0;
}

//no-sort
.no-sort {
    &::before,
    &::after {
        display: none !important;
    }
}

//selected
.table {
    tbody {
        tr.selected {
            background-color: #e5faff;
        }
    }
}

.check-row-box {
    height: 18px;
    width: 18px;
    border: 1px solid #707070;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
        display: none;
    }
}

.selected {
    .check-row-box {
        background-color: var(--primary-color);
        border-color: var(--primary-color);
        color: $white;
        i {
            display: block;
        }
    }
}

//custom table
.custom-table {
    margin-top: 0 !important;
    thead {
        tr {
            th {
                color: var(--primary-color);
                border-bottom: 2px solid $gray-200;
                border-top: 0;
                &:focus {
                    outline: none !important;
                }
            }
        }
    }
    tbody {
        tr {
            td {
                border: 0;
                border-top: 1px solid $gray-200;
            }
            &:hover {
                background: $gray-100;
            }
        }
    }
    &.no-hover {
        tbody {
            tr {
                &:hover {
                    background: transparent;
                }
            }
        }
    }
    thead tr th,
    tbody tr td {
        font-size: 14px;
        font-weight: 500;
        @include media-breakpoint-up(lg) {
            font-size: 15px;
        }
        @include media-breakpoint-up(xxxl) {
            font-size: 17px;
        }
    }
    &.gap-adjust {
        tr {
            td {
                padding-top: 0;
                &:first-child {
                    padding-left: 0;
                }
            }
        }
    }
    &.dataTable {
        thead {
            .sorting,
            .sorting_asc,
            .sorting_desc,
            .sorting_asc_disabled,
            .sorting_desc_disabled {
                &:after,
                &:before {
                    font-family: $font-awesome-family;
                    font-weight: 400;
                    bottom: 18px;
                    top: auto;
                    font-size: 15px;
                    opacity: .3;
                }
                &:after {
                    content: "\f175";
                    right: 0.5em;
                }
                &:before {
                    content: "\f176";
                    right: 1.2em;
                }
            }
            .sorting_asc{
                &:before{
                    opacity: 1;
                }
               
            }
            .sorting_desc{
                &:after{
                    opacity: 1;
                }
            }
        }
    }
    .table-img {
        box-shadow: 0px 3px 10px #0000000D;
        height: 62px;
        width: 62px;
        border-radius: 10px;
        overflow: hidden;
        line-height: 60px;
        text-align: center;
        position: relative;
        img {
            max-height: 100%;
            max-width: 100%;
        }
        &.table-img-l {
            width: 100px;
        }
        &.table-img-sm{
            height: 42px;
            width: 42px;
            line-height: 40px;
        }
    }
}

div.dataTables_wrapper div.dataTables_paginate {
    margin-top: 8px;
    @include media-breakpoint-up(md) {
        float: right;
    }
    ul.pagination {
        justify-content: center;
    }
}

table.dataTable {
    border-bottom: 2px solid $gray-200;
    border-collapse: collapse !important;
}

.dt-info {
    font-size: 14px;
    color: $gray-600;
}
.modal {
    .dataTables_filter {
        input {
            max-width: 150px;
        }
    }
}

.dt-length {
    font-size: 14px;
    color: $gray-600;
    // display: flex;
    // white-space: nowrap;
    // align-items: center;
    // justify-content: center;
    @include media-breakpoint-up(md) {
        justify-content: flex-start;
        float: left;
        clear: left;
        padding-top: 7px;
        margin-bottom: 25px;
    }
    label {
        margin-bottom: 0;
    }
    .dt-input {
        margin-left: 15px;
        min-width: 75px;
        appearance: menulist;
        -moz-appearance: menulist;
        -webkit-appearance: menulist;    
        background-image: none;
        border: 1px solid $gray-400;
        border-radius: 0.2rem;
        font-size: .7875rem;
        height: calc(1.68125rem + 2px);
        line-height: 1.5;
        background-color: $white;
        color: $gray-700;
        width: auto;
        @extend .form-select;
        @extend .form-select-sm;
        display: inline-block;
    }
}

.dt-buttons {
    @include media-breakpoint-up(md) {
        text-align: right;
    }
    .dt-button {
        &+.dt-button {
            margin-left: 10px;
        }
    }
}
.dt-empty{
    text-align: center;
}
.pagination-top {
    @include media-breakpoint-up(md) {
        display: flex;
        justify-content: space-between;
        text-align: left;
        align-items: center;
    }
    .dt-buttons {
        padding: 10px 0;
        .btn {
            &+.btn {
                margin-left: 10px;
            }
        }
    }
}

.pagination-wrap {
    padding-top: 5px;
    text-align: center;
    // .pagination-left {
    //     display: block;
        @include media-breakpoint-up(sm) {
            justify-content: space-between;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            text-align: left;
        }
    // }
}

#unselectedQuestions-pagination,
#selectedQuestions-pagination {
    padding: 0;
    margin: 0;
    padding-top: 10px;
}

.dt-paging {
    margin-bottom: 0;
    justify-content: center;
    margin-top: 10px;
    @include media-breakpoint-up(sm) {
        margin-top: 0;
        justify-content: initial;
    }
    button {
        align-self: center;
    }
    .page-arrow {
        font-size: 16px;
    }
    .dt-paging-button {
        border: 0;
        padding: 4px 10px;
        color: $gray-600;
        font-size: 14px;
        background-color: transparent;
        &.first , &.last{
            display: none;
        }
        &.current {
            color: var(--primary-color);
            background-color: $white;
            box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.07);
            border-radius: 6px;
            font-weight: 600;
        }
        &:hover {
            color: var(--primary-color);
            background: transparent;
        }
        &:focus {
            box-shadow: none;
        }
    }
}

.action-icon {
    &:not(:last-child) {
        margin-right: 10px;
    }
    &.danger {
        color: $orange;
    }
    &.text-info {
        color: $cyan;
    }
    &.font-16{
        font-size: 16px;
    }
}

.round-score {
    display: inline-block;
    border-radius: 100px;
    min-width: 50px;
    color: #fff;
    padding: 5px 10px;
}
.no-data-table{
    border-bottom: 2px solid $gray-200;
}

/* adjust height of dropdown 
in case table height is less and dropdown is getting cut or causing scrollbar*/
.adjust-dropdown-height .dropdown-menu {
    max-height: 150px;
    overflow: hidden;
    overflow-y: auto;
}