/*
 * Component: Form
 * ---------------
 */

.form-control::-webkit-input-placeholder {
    /* Edge */
    color: $input-placeholder-color;
}

.form-control:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: $input-placeholder-color;
}

.form-control::placeholder {
    color: $input-placeholder-color;
}
.select2-search__field::-webkit-input-placeholder,
.select2-search__field::placeholder {
    color: $input-placeholder-color !important;
    padding-left: 12px;
}
.form-control:focus {
    box-shadow: none;
}
.form-group {
    margin-bottom: 15px;
    @include media-breakpoint-up(lg) {
        margin-bottom: 20px;
    }
    @include media-breakpoint-up(xxxl) {
        margin-bottom: 30px;
    }
    label {
        font-size: 14px;
        color: $gray-900;
        margin-bottom: 0.5rem;
    }
    .form-control {
        padding: 10px 15px;
        height: initial;
        height: auto;
        line-height: 1;
        border-radius: 6px;
        border-color: $gray-300;
        box-shadow: none;
        // font-weight: 500;
        background-color: $white;
        @include media-breakpoint-up(lg) {
            padding: 12px 20px;
        }
        &:focus {
            border-color: var(--primary-color);
        }
        // &[readonly] {
        //     opacity: 0.6;
        // }
        &:disabled,
        &[readonly] {
            background-color: $input-disabled-bg;
            opacity: 1;
            border-color: $gray-400;
        }
        &.is-valid:focus{
            box-shadow: none;
        }
        &.datepicker{
            background-color: $white;
            &[readonly] {
                background-color: $white;
                color: $gray-1000;
                border-color: $gray-300;
                &:focus{
                    border-color: var(--primary-color);
                }
            }
            &:disabled{
                background-color: $input-disabled-bg;
                opacity: 1;
                border-color: $gray-400;
                &:focus{
                    border-color: var(--primary-color);
                }
            }
        }
        // invalid code
        &.element-invalid{
            border-color: $red;
        }
    }
    // select2 invalid code
    .select2.element-invalid ~ .select2-container--default .select2-selection--single{
        border-color: $red;
    }
    .input-group-text {
        border: 0;
    }
    textarea {
        &.form-control {
            height: 90px;
            resize: none;
            line-height: 1.3;
        }
    }
    &.has-icon {
        position: relative;
        .form-control {
            padding-right: 35px;
        }
        .form-icon {
            cursor: pointer;
            position: absolute;
            right: 15px;
            top: 50%;
            border: 0;
            background-color: transparent;
            font-size: 18px;
            padding: 4px;
            @include transform(translate(0,-50%));
            color: $gray-900;
        }
    }
    .select2-container--default.select2-container--focus, .select2-container--default:active, .select2-container--default:focus, .select2-selection.select2-container--focus, .select2-selection:active, .select2-selection:focus{
        border-color: var(--primary-color);
    }
    .select2-container--default.select2-container--open.select2-container--below.select2-container--open .select2-selection--single,
    .select2-container--default.select2-container--open .select2-selection--multiple {
        box-shadow: none;
        border-color: var(--primary-color);
    }
    .select2-container--default.select2-container--focus .select2-selection--multiple{
        border-color: var(--primary-color);
    }
    .select2-container .select2-selection--multiple{
        min-height: 39px;
        @include media-breakpoint-up(lg){
            min-height: 44px;
        }
    }
    .select2-container--default .select2-selection--multiple .select2-selection__choice{
        margin-top: 6px;
        white-space: initial;
        @include media-breakpoint-up(lg){
            margin-top: 8px;
        }
    }
    .select2-container--default .select2-selection--multiple .select2-selection__clear{
        top: 5px;
        @include media-breakpoint-up(lg){
            top: 7px;
        }
    }

    .select2-container--default .select2-search--inline .select2-search__field{
        opacity: 1;
        padding: 1px 2px;
        margin-top: 8px;
        @include media-breakpoint-up(lg){
            margin-top: 9px;
        }
    }
    /*disabled select2*/
    .select2-container--default.select2-container--disabled .select2-selection--single,.select2-container--default.select2-container--disabled .select2-selection--multiple{
        background-color: $input-disabled-bg;
        opacity: 1;
        border-color: $gray-400;
        // color: $input-placeholder-color;
    }
}

.custom-file {
    height: 44px;
    position: relative;
    width: 100%;
    display: inline-block;
    .custom-file-label {
        margin: 0;
        background: $white;
        height: 100%;
        border-radius: 6px;
        padding: 12px 20px;
        padding-right: 105px;
        min-height: 44px;
        position: absolute;
        right: 0;
        top: 0;
        left: 0;
        border: 1px solid $gray-300;
        &:after {
            padding: 12px;
            background-color: $gray-200;
            border-left: 0;
            border-radius: 6px;
            padding: 12px 22px;
            color: var(--primary-color);
            height: initial;
            line-height: 1.3;
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            content: "Browse";
            display: block;
            z-index: 3;
        }
    }
    input.form-control{
        height: 100%;
    }
    // .invalid-feedback {
    //     position: absolute;
    // }
}
.ui-timepicker-select {
    display: block;
    width: 100%;
    padding: 10px 15px;
    height: initial;
    height: auto;
    font-size: 15px;
    line-height: 1;
    border-radius: 6px;
    color: $gray-1000;
    background-color: $white;
    background-clip: padding-box;
    border: 1px solid $gray-300;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin-right: 15px;
}

.ui-timepicker-select:focus {
    border-color: var(--primary-color);
    outline: 0;
}
select[name="ui-timepicker-timeTo"] {
    background-color: $input-disabled-bg;
    opacity: 1;
    border-color: $gray-400;
    color: $input-placeholder-color;
}
