/* button groups */

.btn-group-vertical {
    .btn {
        &.btn-flat:first-of-type,
        &.btn-flat:last-of-type {
            @include border-radius(0);
        }
    }
}


/* Support Font Awesome icons in form-control */

.form-control-feedback.fa {
    line-height: $input-height;
}

.input-lg+.form-control-feedback.fa,
.input-group-lg+.form-control-feedback.fa,
.form-group-lg .form-control+.form-control-feedback.fa {
    line-height: $input-height-lg;
}

.input-sm+.form-control-feedback.fa,
.input-group-sm+.form-control-feedback.fa,
.form-group-sm .form-control+.form-control-feedback.fa {
    line-height: $input-height-sm;
}

label:not(.form-check-label, .custom-file-label) {
    font-weight: $font-weight-normal-bold;
}

@include media-breakpoint-up(md) {
    .tree-multiselect-box {
        max-height: 300px;
        height: 300px;
        overflow-y: auto;
    }
}

.inline-select-filead {
    display: flex;
    align-items: center;
    .left-item-filead {
        min-width: 66px;
    }
    .right-item-filead {
        width: 100%;
    }
}

//daterange
.input-daterange .input-group-addon {
    // height: 35px;
    // width: 45px;
    // line-height: 28px;
    width: 30px;
    height: 100%;
    line-height: initial;
    background: transparent;
    border: 0;
}

.daterange-outer {
    @include media-breakpoint-up(md) {
        min-width: calc((255px * 2) + 20px);
    }
    .input-daterange {
        display: flex;
        width: 100%;
        align-items: center;
        .form-control {
            border-radius: 6px !important;
            text-align: left;
            &[readonly] {
                background-color: $white;
                color: $gray-1000;
                border-color: $gray-300;
                opacity: 1;
                &:focus{
                    border-color: var(--primary-color);
                }
            }
            &:disabled{
                background-color: $input-disabled-bg;
                opacity: 1;
                border-color: $gray-400;
                &:focus{
                    border-color: var(--primary-color);
                }
            }
        }
    }
}

.dateranges {
    .form-control[readonly] {
        opacity: 1;
    }
}

//Search Form
.search-card.card {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 100;
    margin-bottom: 0 !important;
    border-radius: 0;
    overflow: hidden;
    overflow-y: auto;
    padding: 20px 10px 50px;
    @include media-breakpoint-up(md) {
        position: static;
        overflow: hidden;
        display: block;
        padding: 0;
        margin-bottom: 1rem !important;
        border-radius: 20px;
    }
    h4 {
        margin-bottom: 20px;
    }
}

.filter-btn.btn {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 99;
    right: 0;
    border-radius: 0;
    width: 100%;
    @include media-breakpoint-up(md) {
        display: none;
    }
}

.search-outer {
    >div {
        align-self: center;
        div {
            display: inline-block;
            margin-right: 0;
            margin-bottom: 15px;
            vertical-align: middle;
            width: 100%;
            @include media-breakpoint-up(md) {
                width: 255px;
                margin-right: 15px;
            }
        }
    }
    .search-actions {
        min-width: 125px;
        position: fixed;
        z-index: 101;
        bottom: 0;
        left: 0;
        text-align: center;
        margin: 0;
        display: flex;
        right: 0;
        @include media-breakpoint-up(md) {
            position: static;
            display: initial;
            margin-top: 4px;
            flex-shrink: 0;
            margin-bottom: 15px;
            text-align: left;
            min-width: 145px;
        }
        @include media-breakpoint-up(lg) {
            min-width: 165px;
            margin-top: 6px;
        }
        @include media-breakpoint-up(xxxl) {
            min-width: 185px;
        }
        a,
        button {
            font-size: 14px;
            width: 50%;
            text-align: center;
            padding: 8px 20px;
            @include media-breakpoint-up(md) {
                font-size: 16px;
                padding: 4px;
                width: initial;
            }
            @include media-breakpoint-up(xxxl) {
                font-size: 18px;
            }
            &.filter-apply-btn {
                border: 0;
                font-weight: 500;
                background: var(--primary-color);
                color: $white;
                cursor: pointer;
                outline: 0;
                &:hover {
                    color: var(--primary-color-darken);
                }
                @include media-breakpoint-up(md) {
                    color: var(--primary-color);
                    background: transparent;
                }
            }
        }
        .filter-cancel-icon {
            color: #82808e;
            vertical-align: middle;
            background: $white;
            border: 1px solid $gray-200;
            @include media-breakpoint-up(md) {
                border: 0;
            }
            &:hover {
                color: $gray-1000;
            }
        }
    }
}

// Badge 
.badge.badge-outline {
    display: inline-block;
    padding: 7px 10px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: 20px;
    border: 1px solid $gray-200;
    @include media-breakpoint-up(xxxl) {
        font-size: 16px;
        padding: 10px 16px;
    }
}

.btn.badge-btn {
    padding: 7px 19px;
    line-height: 1;
    @include media-breakpoint-up(xxxl) {
        padding: 10px 30px;
        line-height: 1;
    }
}

// tree multiselect
div.tree-multiselect {
    border: 0;
    .select-all-container {
        font-size: 12px;
        color: $gray-600;
        @include media-breakpoint-up(xxxl) {
            font-size: 14px;
        }
    }
    div.title {
        background: transparent;
        color: $gray-1000;
        font-size: 16px;
        padding: 12px 4px;
        display: flex;
        align-items: center;
        @include media-breakpoint-up(xxxl) {
            font-size: 18px;
        }
        &>span.collapse-section {
            margin: 0 15px 0 0;
            font-size: 17px;
            line-height: 1;
            font-weight: 400;
            color: $gray-600;
            flex-shrink: 0;
            width: 13px;
            vertical-align: middle;
            @include media-breakpoint-up(xxxl) {
                font-size: 19px;
                margin: 0 20px 0 0;
            }
            &:after {
                content: "\f068";
                font-family: $font-awesome-family;
            }
        }
        &>input.section{
            flex-shrink: 0;
        }
    }
    div.section {
        &:not(:last-child) {
            border-bottom: 1px solid $gray-200;
        }
        &.collapsed {
            &>div.title {
                span.collapse-section {
                    &:after {
                        content: "\f067";
                        font-family: $font-awesome-family;
                    }
                }
            }
        }
    }
    div.selections {
        border-color: $gray-200;
    }
    .selections {
        div.item {
            background-color: $gray-100;
            padding: 6px 3px !important;
            margin-left: 32px !important;
            @include media-breakpoint-up(xxxl) {
                margin-left: 36px !important;
            }
            .option{
                align-items: flex-start;
                flex-shrink: 0;
            }
            label {
                font-weight: 500;
                font-size: 14px;
                @include media-breakpoint-up(xxxl) {
                    font-size: 16px;
                }
            }
        }
    }
    input[type="checkbox"] {
        margin-right: 15px;
        height: 17px;
        width: 17px;
        vertical-align: middle;
    }
    .auxiliary {
        input.search {
            border: 1px solid $gray-200;
            padding: 10px 15px;
            height: initial;
            @include media-breakpoint-up(lg) {
                padding: 12px 20px;
            }
        }
    }
    //right section
    &>div.selected {
        &>div.item {
            background: $gray-100;
            padding: 6px 5px 6px 35px;
            position: relative;
            font-size: 14px;
            @include media-breakpoint-up(xxxl) {
                font-size: 16px;
            }
        }
    }
    span {
        &.section-name {
            color: $gray-600;
            margin-top: 2px;
            font-size: 11px;
            @include media-breakpoint-up(xxxl) {
                font-size: 13px;
            }
        }
        &.remove-selected {
            margin-right: 15px;
            position: absolute;
            left: 5px;
            background-color: $danger;
        }
    }
}

//Step wizard
.form-card{
    .wizard{
        .wizard-inner {
            .nav-tabs,.wiz-tablist {
                border: 0;
                display: flex;
                // justify-content: space-between;
                max-width: 100%;
                position: relative;
                list-style-type: none;
                margin: 0 auto;
                padding: 0;
                padding-top: 30px;
                li {
                    max-width: 50%;
                    text-align: center;
                    flex: 1;
                    position: relative;
                    .current-info{
                        display: none;
                    }
                    a {
                        display: flex;
                        flex-direction: column;
                        color: #ACA9B9;
                        background: transparent;
                        padding: 0;
                        i {
                            font-style: normal;
                            text-transform: uppercase;
                            font-size: 12px;
                            font-weight: 500;
                        }
                        span.round-tab,.number {
                            width: 30px;
                            height: 30px;
                            line-height: 30px;
                            display: block;
                            border-radius: 50%;
                            background: #EBECF0;
                            border: 6px solid #fff;
                            position: relative;
                            @include transform(scale(0.6));
                            margin: 0 auto 5px;
                            z-index: 3;
                            text-indent: -9999px;
                            &:after {
                                left: 6px;
                                top: 3px;
                                width: 5px;
                                height: 10px;
                                border: solid #fff;
                                border-width: 0 2px 2px 0;
                                transform: rotate( 45deg);
                                content: "";
                                position: absolute;
                                opacity: 0;
                            }
                        }
                    }
                    &.active,&.current {
                        a {
                            color: #5B5865;
                            span.round-tab,.number {
                                background: $cyan;
                                @include transform(scale(0.6));
                            }
                        }
                    }
                    &.error{
                        a {
                            span.round-tab,.number {
                                background: $red;
                                @include transform(scale(0.6));
                            }
                        }
                    }
                    &.completed,&.done {
                        a {
                            color: #5B5865;
                            span.round-tab,.number {
                                background: $cyan;
                                box-shadow: 0 3px 20px #00000029;
                                @include transform(scale(1));
                                &:after {
                                    opacity: 1;
                                }
                            }
                        }
                       /* &:before {
                            background: $cyan;
                            z-index: 2;
                        } */
                    }
                    &:before,
                    &:after {
                        position: absolute;
                        content: "";
                        height: 4px;
                        top: 15px;
                        margin-top: -2px;
                    }
                    &:before {
                        width: 100%;
                        background-color: $gray-200;
                    }
                    &:after {
                        left: 50%;
                        width: 40px;
                        background: $white;
                        z-index: 2;
                        margin-left: -20px;
                    }
                    &:last-child {
                        &:before {
                            display: none;
                        }
                    }
                    &.disabled {
                        pointer-events: none;
                    }
                }
            }
        }
        >.content{
            background-color: transparent;
            margin: 0;
            margin-top: 20px;
            border-top: 1px solid $gray-200;
            border-radius: 0;
            &>.body{
                float: none;
                padding: 0;
                position: static;
                width: 100%;
                height: 100%;
                input {
                    display: block;
                    border: 1px solid $gray-300;
                    &:focus {
                        border-color: var(--primary-color);
                    }
                }
                .tree-multiselect{
                    input[type='checkbox']{
                        display: inline-block
                    }
                }
            }
        }
        >.actions {
            @extend .card-footer;
            ul{
                display: flex;
                li{
                    &:first-child{
                        margin-right: auto;
                    }
                    a{
                        background-color: var(--primary-color);
                        border-radius: 25px;
                        padding: 8px 20px;
                    }
                    &.disabled{
                        a{
                            background-color: $gray-200;
                        }
                    }
                }
            }
           
        }


    }
    .vertical-stepwizard{
        .wizard{
            .wizard-inner {
                .nav-tabs,.wiz-tablist {
                    display: flex;
                    flex-direction: column;
                    padding: 0;
                    li {
                        text-align: left;
                        max-width: 100%;
                        &:not(:last-child){
                            padding: 0 0 40px 0;
                        }
                        a {
                            display: flex;
                            align-items: center;
                            flex-direction: row;
                            span.round-tab,.number {
                                margin: 0 8px 5px 0;
                            }
                        }
                        &:before,
                        &:after {
                            position: absolute;
                            content: "";
                            width: 4px;
                            height: 0;
                            left: 15px;
                            margin-left: -2px;
                            transition: all 0.3s;
                        }
                        &:before {
                            height: 100%;
                        }
                        &:after {
                            height: 0;
                            background: $cyan;
                            z-index: 2;
                            margin-left: -2px;
                        }
                        &.completed,&.done {
                            &:after {
                                height: 100%;
                            }
                        }
                        &:last-child.completed:after {
                            display: none;
                        }
                    }
                }
            }
        }
        .stepwizard-content-wrapper{
            .steps{
                display:none;
            }
            .content{
                margin:0;
                border:0;
            }
            .actions {
                padding:0;
                border:0;
                ul{
                    padding:0;
                    margin:0;
                    margin-top: 30px;
                    li{
                        list-style-type:none;
                        .btn[href="#previous"]{
                            @extend .btn-primary;
                            color: $white !important;
                        }
                        .btn[href="#next"]{
                            @extend .btn-primary;
                            color: $white !important;
                        }
                        .btn[href="#finish"]{
                            @extend .btn-primary;
                            color: $white !important;
                        }
                    }
                }
            }
            .wizard{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: 100%;
                .clearfix{
                    &:after{
                        display: none;
                    }
                }
                &.clearfix{
                    &:after{
                        display: none;
                    }
                }
            }
        }
    }
}

.custom-file, .custom-file-input {
    // height: calc(2.1rem + 2px);
    position: relative;
    width: 100%;
}
.datepicker-wrap {
    position: relative;
    .form-control {
        padding-right: 45px;
        @include media-breakpoint-up(lg) {
            padding-right: 50px;
        }
        &:read-only {
            opacity: 1;
        }
    }
    i {
        position: absolute;
        top: 15px;
        right: 15px;
        pointer-events: none;
        @include media-breakpoint-up(lg) {
            right: 20px;
        }
    }
}
.error-feedback {
    width: 100%;
    margin-top: 0.25rem;
    font-size: 80%;
    color: $red;
}

.staff-card{
    padding: 20px;
    border: 1px solid $gray-200;
    background: $gray-100;
}

span.service-badge {
    border: 1px solid #ddd;
    padding: 4px 7px;
    background: #ffffff;
    margin-right: 10px;
    border-radius: 3px;
    display: inline-block;
    margin-bottom: 7px;
}

span.service-badge i {
    opacity: 0.6;
    margin-left: 10px;
    cursor: pointer;
    vertical-align: middle;
}
.font-16.float-end ~ .custom-file{
    clear: both;
}