/* Tab-2 Nourish Insights */

.total-recipes-chart {
    margin: 0 auto 10px;
    text-align: center;
}

.recipe-viewed {
    text-align: center;
    margin-bottom: 30px;
    .recipe-viewed-text {
        display: inline-block;
        position: relative;
        padding-left: 24px;
        font-size: 16px;
        font-weight: 500;
        &::before {
            content: '';
            position: absolute;
            top: 3px;
            left: 0;
            background: white;
            width: 16px;
            height: 16px;
            border-radius: 10px;
            border: 4px solid #ffb35e;
        }
        span {
            color: #707070;
            font-size: 20px;
            min-width: 70px;
            display: inline-block;
            text-align: left;
            line-height: 1.1;
        }
    }
}

.total-recipes-count {
    font-size: 30px;
    color: var(--primary-color);
    font-weight: 600;
    margin-bottom: 0;
}

.total-recipes-text {
    font-weight: 500;
    color: #231e21;
    span {
        color: #a1a1a1;
        font-size: 12px;
    }
}

