.superstars-box {
    .superstars-box-title {
        padding: 15px;
        border-radius: 10px;
        color: $white;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        @include media-breakpoint-up(lg) {
            font-size: 16px;
            padding: 20px;
        }
        @include media-breakpoint-up(xxxl) {
            font-size: 18px;
        }
        .tooltip-icon {
            color: $white;
        }
        &.active-team {
            background-color: var(--primary-color);
        }
        &.active-individual {
            background-color: $cyan;
        }
        &.badges-earned {
            background-color: $orange;
        }
    }
    .sx-list {
        padding: 0;
        list-style: none;
        li {
            display: flex;
            align-items: center;
            width: 100%;
            padding: 10px 5px;
            border-bottom: 1px solid $gray-200;
            &:last-child {
                border-bottom: 0;
            }
            .sx-user-img {
                width: 50px;
                height: 50px;
                border-radius: 100%;
                margin-right: 15px;
                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 100%;
                    object-fit: cover;
                }
            }
            .sx-list-name-area {
                small {
                    color: #a1a1a1;
                    font-size: 13px;
                    font-weight: 500;
                    @include media-breakpoint-up(xxxl) {
                        font-size: 14px;
                    }
                }
            }
            .sx-list-name {
                color: $gray-1000;
                margin-bottom: 0px;
                line-height: 1;
                margin-bottom: 0;
                font-size: 14px;
                @include media-breakpoint-up(lg) {
                    font-size: 16px;
                    margin-bottom: 5px;
                }
                @include media-breakpoint-up(xxxl) {
                    font-size: 18px;
                }
            }
            .sx-list-hr {
                margin-left: auto;
                color: #a1a1a1;
            }
        }
    }
}

