.tabs-wraper {
    padding: 0 50px;
    border-bottom: 2px solid #D7D3C7;
    margin: 0 -25px 35px;
    .owl-nav {
        margin: 0;
    }
    .owl-carousel {
        margin-bottom: -2px;
        .owl-stage {
            display: flex;
        }
        .owl-item {
            .item {
                padding: 30px 15px 30px;
                height: 100%;
                border-bottom: 5px solid transparent;
                @include media-breakpoint-up(xxxl) {
                    padding: 40px 15px 40px;
                }
                &.selected {
                    border-bottom: 5px solid var(--primary-color);
                }
                a {
                    cursor: pointer;
                    color: $gray-800;
                    font-size: 14px;
                    @include media-breakpoint-up(md) {
                        font-size: 16px;
                    }
                    @include media-breakpoint-up(xxxl) {
                        font-size: 18px;
                    }
                    .badge {
                        position: absolute;
                        left: 50%;
                        bottom: 13px;
                        margin: 0 auto;
                        display: inline-block;
                        -webkit-transform: translateX(-50%);
                        -moz-transform: translateX(-50%);
                        -ms-transform: translateX(-50%);
                        transform: translateX(-50%);
                    }
                }
                img {
                    margin-bottom: 15px;
                    max-width: 60px;
                    border-radius: 100%;
                    box-shadow: 0px 3px 20px #0000000D;
                    @include media-breakpoint-up(md) {
                        max-width: 70px;
                    }
                    @include media-breakpoint-up(xxxl) {
                        max-width: 80px;
                    }
                }
            }
        }
        .owl-nav {
            button.owl-next,
            button.owl-prev {
                position: absolute;
                top: 50%;
                @include transform(translateY(-50%));
                margin: 0;
                padding: 3px !important;
                display: block;
                cursor: pointer;
                line-height: 1;
                font-size: 26px;
            }
            button.owl-next {
                right: -25px;
            }
            button.owl-prev {
                left: -25px;
            }
        }
    }
    &.tabs-wrapper-small {
        padding: 0 25px;
        margin: 0;
        .owl-carousel {
            padding: 0;
            .owl-item {
                .item {
                    padding: 10px 5px 10px;
                    a {
                        color: $gray-500;
                    }
                    &.selected {
                        a {
                            color: $gray-1000;
                        }
                    }
                }
            }
            .owl-nav {
                button {
                    font-size: 15px;
                }
            }
        }
    }
}

