    }
    .card-body {
        @include media-breakpoint-up(md) {
            padding: 0;
        }
        .search-outer {
            &>div {
                @include media-breakpoint-up(md) {
                    display: flex;
                }
                div {
                    @include media-breakpoint-up(md) {
                        margin-right: 0;
                        margin-bottom: 0;
                    }
                }
            }
        }
        .form-group {
            @include media-breakpoint-up(md) {
                &:not(:last-child) {
                    border-right: 1px solid $gray-200;
                }
                .select2-container--default .select2-selection--single {
                    border-width: 0;
                }
            }
