.week-month-year-list {
    padding: 0;
    margin: 0 0 6px;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    li {
        list-style: none;
        color: $gray-900;
        margin-left: 8px;
        margin-right: 0;
        cursor: pointer;
        border: 1px solid transparent;
        border-radius: 20px;
        padding: 4px 8px;
        line-height: 1.6;
