.dashboard-card {
    margin-bottom: 0;
    position: relative;
    @include media-breakpoint-up(xl) {
        height: 100%;
    }
    .card-body {
        padding: 20px;
        @include media-breakpoint-up(xl) {
            padding: 25px;
        }
        .nav-tabs {
            margin: 0 -20px 30px;
            @include media-breakpoint-up(xl) {
                margin: 0 -25px 30px;
            }
            .nav-item {
                margin: 0 10px 0 0;
                .nav-link {
                    padding: 8px 20px;
                    @include media-breakpoint-up(xl) {
                        padding: 10px 25px;
                    }
                }
            }
        }
        .dashboard-table-height {
            max-height: 420px;
            overflow-y: auto;
        }
    }
    &.has-bg {
        &:after {
            position: absolute;
            bottom: 0;
            right: 0;
            content: "\f500";
            font-family: $font-awesome-family;
            font-weight: 300;
            font-size: 60px;
            color: #F6F5F2;
            margin-right: 15px;
            @include media-breakpoint-up(lg) {
                font-size: 70px;
            }
            @include media-breakpoint-up(xxxl) {
                font-size: 80px;
            }
            @include media-breakpoint-up(xxxl) {
                font-size: 90px;
            }
        }
        .card-body {
            position: relative;
            z-index: 2;
        }
    }
    .data-centered {
        justify-content: space-between;
        display: flex;
        flex-direction: column;
    }
    .tooltip-icon {
        font-size: 16px;
        @include media-breakpoint-up(xl) {
            font-size: 18px;
        }
    }
}

.info-block {
    border: 1px solid #ACA9B9;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    color: $gray-1000;
    max-width: 250px;
    justify-content: space-between;
    font-size: 16px;
    @include media-breakpoint-up(lg) {
        padding: 10px;
    }
    @include media-breakpoint-up(xxxl) {
        font-size: 18px;
        padding: 15px;
    }
    span {
        align-self: center;
    }
    .info-block-value {
        font-size: 22px;
        display: block;
        @include media-breakpoint-up(sm) {
            display: inline-block;
        }
        @include media-breakpoint-up(xxxl) {
            font-size: 26px;
        }
    }
    &+.info-block {
        margin-top: 20px;
        @include media-breakpoint-up(xxxl) {
            margin-top: 30px;
        }
    }
    &.no-border {
        border: 0;
        padding: 10px 0;
        @include media-breakpoint-up(lg) {
            padding: 10px;
        }
        @include media-breakpoint-up(xxxl) {
            padding: 15px;
        }
    }
    &.stacked {
        flex-direction: column;
        color: $gray-700;
        &>div {
            display: flex;
            justify-content: space-between;
            &:not(:last-child) {
                margin-bottom: 15px;
