.modal{
    .modal-content{
        border: 0;
        @include box-shadow(0 3px 16px rgba(60,76,91,0.16));
        border-radius: 10px;
        .modal-header{
            background: var(--primary-color);
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            padding: 25px 20px;
            justify-content: space-between;
            @include media-breakpoint-up(xxxl) {
                padding: 35px 30px;
            }
            a{
                color: $white;  
            }
            .modal-title{
                color: $white;
                font-size: 16px;
                font-weight: 600;
                @include media-breakpoint-up(xxxl) {
                    font-size: 18px;
                }
            }
            .close{
                color: $white;
                box-shadow: none;
                opacity: 1;
                font-size: 16px;
                float: none;
                align-self: center;
                background: transparent;
                outline: 0;
                border: 0;
                padding: 0 4px;
                display: inline-block;
                @include media-breakpoint-up(xxxl) {
                    font-size: 18px;
                }
            }
        }
        .modal-body{
            padding: 20px;
            @include media-breakpoint-up(xxxl) {
                padding: 30px;
                font-size: 16px;
            }
        }
        .modal-footer{
            justify-content: space-between;
            padding: 15px 20px;
            @include media-breakpoint-up(xxxl) {
                padding: 20px 30px;
            }
        }
    }
    &.has-solid-bg{
        background-color: #333333;
    }
    &.show{
        // audio video options fix
        .modal-dialog{
            transform: none!important;
        }
    }
}
.full-screen-popup {
    overflow: hidden !important;
    padding: 0px !important;
    z-index: 1090;
  
    .modal-content {
      border: none;
    }
  
    .modal-dialog {
      max-width: 100%;
      margin: 0;
      border-radius: 0;
      box-shadow: none;
    }
  
    .modal-area {
      max-height: 100vh;
      min-height: 100vh;
      overflow: auto;
    }
  
    .modal-header {
      background: #00a7d2;
      color: #fff;
      box-shadow: 2px 11px 20px 0px rgba(0, 0, 0, 0.14);
      border-radius: 0;
      border: none;
      margin-bottom: 8%;
  
      .close {
        color: #fff;
        opacity: 1;
        font-size: 1.5rem;
      }
  
      .close:not(:disabled):not(.disabled):hover {
        color: #ffffff;
      }
    }
  .mus-player{
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
  }
}