/*
 * Core: General Layout Style
 * -------------------------
 */

body,
.wrapper {
    min-height: 100%;
    overflow-x: hidden;
    background: $main-bg; 
    &.is-filter-open{
        overflow: hidden;
    }
}
body{
    font-size: 0.9rem;
}

:root {
    --primary-color: #5261ac;
    --primary-color-darken: #394478;//     var(--color-primary) * 15% darker
}

.wrapper {
    position: relative;
}

a{
    display: inline-block;
}
a:not([href]):not([tabindex]), a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
    color: inherit;
    text-decoration: none;
}
h1{
    font-size: 30px;
    @include media-breakpoint-up(md) {
        font-size: 32px;
    }
}
h2{
    font-size: 26px;
    @include media-breakpoint-up(md) {
        font-size: 28px;
    }
}
h3{
    font-size: 22px;
    @include media-breakpoint-up(md) {
        font-size: 24px;
    }
}
h4{
    font-size: 20px;
    @include media-breakpoint-up(md) {
        font-size: 22px;
    }
}
h5{
    font-size: 16px;
    @include media-breakpoint-up(md) {
        font-size: 18px;
    }    
}
h6{
    font-size: 15px;
}
.content-wrapper,
.main-footer {
    @include media-breakpoint-up(lg) {
        @include transition(margin-left $transition-speed $transition-fn);
        margin-left: $sidebar-width;
        z-index: 3000;
        .sidebar-collapse & {
            margin-left: 0;
        }
    }
    @include media-breakpoint-up(xxxl) {
        margin-left: $sidebar-width-xxl;
    }
}

.content-wrapper {
    background: $main-bg;
    padding-top: 0;
    min-height: -webkit-calc(100vh - 53px) !important;
    min-height: calc(100vh - 53px) !important;
    padding: 10px 0;
    @include media-breakpoint-up(xxxl) {
        padding: 15px 30px;
    }
}

.main-footer {
    padding: 15px;
    color: #ACA9B9;
    background: $main-bg;
    border-top: 1px solid #E0E1E6;
    text-align: center;
    @include media-breakpoint-up(lg) {
        display: flex;
        justify-content: space-between;
    }
}

.content-header {
    margin-bottom: 20px;
    h1 {
        font-size: 16px;
        margin-bottom: 6px;
        @include media-breakpoint-up(lg){
            font-size: 18px;
        }
        @include media-breakpoint-up(xxxl){
            font-size: 22px;
            margin-bottom: 10px;
        }
    }
    .breadcrumb {
        margin-bottom: 0;
        padding: 0;
        background: transparent;
        font-size: 10px;
        @include media-breakpoint-up(lg){
            font-size: 12px;
        }
    }
    .btn{
        margin-top: 15px;
        @include media-breakpoint-up(md){
            margin-top: 0;
        }
        &+ .btn{
            margin-left: 10px;
        }
    }
    .back-link{
        font-size: 14px;
        color: $gray-900;
        &:hover{
            color: var(--primary-color);
        }
    }
}

.breadcrumb-item {
    a {
        color: #a1a1a1;
        &:hover {
            color: var(--primary-color);
        }
    }
    &.active {
        color: $gray-1000;
    }
}
