@font-face {
  font-family: 'icomoon';
  src:  url('#{$custom-icons-font-path}/menu-icons/icomoon.eot?sx63wm');
  src:  url('#{$custom-icons-font-path}/menu-icons/icomoon.eot?sx63wm#iefix') format('embedded-opentype'),
    url('#{$custom-icons-font-path}/menu-icons/icomoon.ttf?sx63wm') format('truetype'),
    url('#{$custom-icons-font-path}/menu-icons/icomoon.woff?sx63wm') format('woff'),
    url('#{$custom-icons-font-path}/menu-icons/icomoon.svg?sx63wm#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

