.survey-list{
    text-align: center;
    .item{       
        p{
            font-size: 14px;
            margin-bottom: 4px;
            @include media-breakpoint-up(lg) {
                font-size: 16px;
            }
            @include media-breakpoint-up(xxxl) {
                font-size: 18px;
            }
            &.value{
                font-size: 16px;
                @include media-breakpoint-up(lg) {
                    font-size: 18px;
                }
                @include media-breakpoint-up(xxxl) {
                    font-size: 20px;
                }
            }
        }
        img{
            max-width: 60px;
            border-radius: 100%;
            margin:0 auto 15px;
            box-shadow: 0px 3px 20px #0000000D;
            @include media-breakpoint-up(md) {
                max-width: 70px;
            }
            @include media-breakpoint-up(xxxl) {
                max-width: 80px;
            }
        }
    }
}
