.label-steps {
  border: 0;
  display: flex;
  list-style-type: none;
  margin: 0 auto;
  max-width: 530px;
  padding: 30px 0 0;
  position: relative;
  div {
    position: relative;
    flex-grow: 1;
    &:not(:last-child):before {
      content: "";
      height: 4px;
      margin-top: -2px;
      position: absolute;
      top: 50%;
      background-color: #eeeff3;
      width: 100%;
      z-index: 0;
    }
    &:last-child {
      flex-grow: initial;
    }
    &.active{
      span{
        background-color: $primary;
        color: $white;
      }
      // &:not(:last-child):before {
      //   background-color: $primary;
      // }
      
    }
    &.disabled{
      span{
        color: #aca9b9;
      }
    }
    &.completed{
      span{
        background-color: $primary;
        color: $white;
      }
      &:before {
        background-color: $primary;
      }
    }
  }
  span {
    position: relative;
    z-index: 2;
    background: #f5f5f5;
    padding: 10px 20px;
    display: inline-block;
  }
}