/*
 * Component: Button
 * -----------------
 */

.btn {
  // Flat buttons
  &.btn-flat {
    @include border-radius(0);
    box-shadow: none;
    border-width: 1px;
  }

  // input file btn
  &.btn-file {
    position: relative;
    overflow: hidden;
    > input[type='file'] {
      position: absolute;
      top: 0;
      right: 0;
      min-width: 100%;
      min-height: 100%;
      font-size: 100px;
      text-align: right;
      opacity: 0;
      outline: none;
      background: white;
      cursor: inherit;
      display: block;
    }
  } 
  font-size: 14px;
  font-weight: 500;
  border-radius: 25px;
  padding: 8px 15px;
  @include media-breakpoint-up(lg){
    padding: 10px 30px;
  }
  @include media-breakpoint-up(xxxl){
    padding: 12px 40px;
  }
  i{
    font-size: 18px;
  }
  span{
    // line-height: 1;
  }
  &:disabled{
    opacity: 0.5;
  }
}

// Button color variations
.btn-default {
  background-color: #f4f4f4;
  color: #444;
  border-color: #ddd;
  &:hover,
  &:active,
  &.hover {
    background-color: darken(#f4f4f4, 5%);
  }
}

// Application buttons
.btn-app {
  @include border-radius(3px);
  position: relative;
  padding: 15px 5px;
  margin: 0 0 10px 10px;
  min-width: 80px;
  height: 60px;
  text-align: center;
  color: #666;
  border: 1px solid #ddd;
  background-color: #f4f4f4;
  font-size: 12px;
  // Icons within the btn
  > .fa,
  > .glyphicon,
  > .ion {
    font-size: 20px;
    display: block;
  }

  &:hover {
    background: #f4f4f4;
    color: #444;
    border-color: #aaa;
  }

  &:active,
  &:focus {
    @include box-shadow(inset 0 3px 5px rgba(0, 0, 0, 0.125));
  }

  // The badge
  > .badge {
    position: absolute;
    top: -3px;
    right: -10px;
    font-size: 10px;
    font-weight: 400;
  }
}

.btn-primary{
      background: var(--primary-color);
      border-color:var(--primary-color);
      color: $white;
      &:disabled{
        color:var(--primary-color);
        background-color: $primary-opacity;
        border-color: transparent;
      }
}


.save-cancel-wrap{
  justify-content: space-between;
  display: flex;
  .btn{
    @include media-breakpoint-up(sm) {
      width:110px;
    }
    @include media-breakpoint-up(md) {
      width:140px;
    }
    @include media-breakpoint-up(xxxl) {
      width:180px;
    }
  }
}
.arrow-btn{
  position: relative;
  min-width: 110px;
  font-size: 12px;
  @include media-breakpoint-up(xxxl) {
    min-width: 140px;
    font-size: 14px;
  }
  .button-arrow{
    background-color: $white;
    box-shadow: 0px 3px 20px #00000029;
    height: 30px;
    width: 30px;
    line-height: 30px;
    color: var(--primary-color);
    font-size: 11px;
    position: absolute;
    border-radius: 100%;
    top: 50%;
    margin-top: -15px;
    i{
      vertical-align: middle;
      font-size: 14px;
    }
  }
}
.right-arrow-btn{
  @extend .arrow-btn;
  padding-right: 45px;
  padding-left: 15px;
  @include media-breakpoint-up(xxxl) {
    padding-left: 30px;
  }
  .button-arrow{
    right: 6px;
  }
}

$newteal: #50C9B5;

.btn-newteal {
  
    @include button-variant($newteal, $newteal, darken($newteal, 10%), darken($newteal, 10%), darken($newteal, 10%), darken($newteal, 10%));
    color: $white;
    .button-arrow{
      color: $newteal;
      line-height: 28px;
    }
}

.prev-step.prev-step-sm{
  position: relative;
  box-shadow: 0px 3px 20px #00000029;
  border: 3px solid $white;
  border-radius: 100%;
  background-color: var(--primary-color);
  color: $white;
  width:auto;
  cursor: pointer;
  padding: 6px 10px;
  @include media-breakpoint-up(lg){
    padding: 5px 9px;
    border: 6px solid $white;
  }
  @include media-breakpoint-up(xxxl){
    padding: 7px 11px;
  }
  .button-arrow{
    font-size: 11px;
    vertical-align: middle;
    i{
      vertical-align: middle;
      font-size: 14px;
    }
  }
}
.left-arrow-btn{
  @extend .arrow-btn;
  padding-left: 45px;
  padding-right: 15px;
  @include media-breakpoint-up(xxxl) {
    padding-right: 30px;
  }
  .button-arrow{
    left: 6px;
    i{
      vertical-align: middle;
      font-size: 14px;
    }
  }
}
.help-outer{
  right: 20px;
    bottom: 20px;
    position: fixed;
    .openwidget {
      padding: 0;
      height: 40px;
      line-height: 40px;
      width: 40px;
      text-align: center;
      border-radius: 100%;
    }
}