.main-header {
  margin-bottom: 24px;
  .hamburger {
    display: flex;
    align-self: center;
    flex-direction: column;
    justify-content: space-between;
    width: 24px;
    height: 17px;
    cursor: pointer;
    margin: 0;
    margin-right: 15px;
    flex-shrink: 0;
    div {
      align-self: flex-end;
      height: 3px;
      width: 100%;
      background: var(--primary-color);
      border-radius: 3px;
      &.meat {
        width: 100%;
        -moz-transition: all 200ms ease-in-out;
        -o-transition: all 200ms ease-in-out;
        -webkit-transition: all 200ms ease-in-out;
        transition: all 200ms ease-in-out;
      }
      &.bottom-bun {
        width: 100%;
        -moz-transition: all 400ms ease-in-out;
        -o-transition: all 400ms ease-in-out;
        -webkit-transition: all 400ms ease-in-out;
        transition: all 400ms ease-in-out;
      }
    }
    
  }
  .worklife-logo {
    img {
      // width: 100%;
      max-height: 38px;
      max-width: 105px;
      display: inline-block;
      @include media-breakpoint-up(md) {
        max-width: initial;
      }
      @include media-breakpoint-up(xxxl) {
        max-height: 58px;
      }
    }
  }
  .profile-header-dropdown {
    i {
      font-size: 15px;
      color: $gray-500;
    }
    .user-image-span {
      margin-top: 0;
      display: inline-block;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      overflow: hidden;
      vertical-align: middle;
      @include media-breakpoint-up(sm) {
        margin-right: 10px;
      }
      @include media-breakpoint-up(md) {
        width: 35px;
        height: 35px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .user-name {
      white-space: nowrap;
      
      max-width: 80px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: none;
      padding-right: 10px;
      color: $gray-900;
      font-size: 15px;
      @include media-breakpoint-up(sm) {
        max-width: 150px;      
        display: inline-block;
      }
    }
    .dropdown-menu {
      box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      border: 0;
      .user-header {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        text-align: center;
        border-bottom: 1px solid $grey-1100;
        img {
          max-height: 210px;
        }
        p {
          padding: 12px 18px;
          color: $gray-900;
          small {
            color: #aaa38e;
            font-size: 12px;
            font-weight: 500;
          }
        }
        .edit-profile{
          i{
            font-size: 14px; 
            margin-left: 5px;
          }
            
           &:hover{
            i{
              color: var(--primary-color);
            }
           }
        }
      }
      .user-footer {
        padding: 22px 18px;
        width: 100%;
        display: flex;
        justify-content: space-between;
      }
    }
  }  
}

.notification-icon {
  font-size: 22px;
  margin-right: 20px;
  color: $gray-500;
  padding: 2px 4px;
  position: relative;
  @include media-breakpoint-up(lg) {
    margin-right: 40px;
  }
  &:after {
    position: absolute;
    content: "";
    right: 2px;
    top: 4px;
    height: 10px;
    width: 10px;
    background: $color-orange;
    border-radius: 100%;
  }
}
