/*
 * Component: Box
 * --------------
 */

.card {
    @extend .mb-3;
    @extend .shadow-sm;
    border: 0;
    &.bg-dark {
        &,
        .card-body {
            color: $white;
        }
    }
    // collapsed mode
    &.collapsed-card {
        .card-body,
        .card-footer {
            display: none;
        }
    }
    .nav.flex-column {
        >li {
            border-bottom: 1px solid $card-border-color;
            margin: 0;
            &:last-of-type {
                border-bottom: none;
            }
        }
    }
    // fixed height to 300px
    &.height-control {
        .card-body {
            max-height: 300px;
            overflow: auto;
        }
    }
    .border-right {
        border-right: 1px solid $card-border-color;
    }
    .border-left {
        border-left: 1px solid $card-border-color;
    }
    // .card-body {
    //     padding: 20px;
    //     @include media-breakpoint-up(md) {
    //         padding: 30px;
    //     }
    //     @include media-breakpoint-up(xxxl) {
    //         padding: 40px;
    //     }
    // }
    &.form-card {
        // min-height: calc(100vh - 220px);
        min-height: 350px;
        .card-body {
            padding: 20px;
            @include media-breakpoint-up(md) {
                padding: 30px;
            }
            @include media-breakpoint-up(xxxl) {
                padding: 40px;
            }
        }
        &.height-auto {
            min-height: initial;
        }
    }
    .card-inner {
        &+.card-inner {
            margin-top: 30px;
            @include media-breakpoint-up(md) {
                margin-top: 40px;
            }
            @include media-breakpoint-up(xxxl) {
                margin-top: 60px;
            }
        }
    }
    .card-img-top{
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        // height: 260px;
        min-height: 335px;
        // object-fit: cover;
        width: 100%;
    }
    &.basic-card{
        height: calc(100% - 30px);
        margin-bottom: 30px !important;
        .card-title{
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .card-body{
            display: flex;
            justify-content: space-between;
            flex-direction: column;
        }
        .btn{
            margin-top: auto;
        }
    }
    .search-in-card.search-card{
        border-radius: 0;
        box-shadow: none;
        @include media-breakpoint-up(md){
            padding: 0;
            background: transparent;
        }
    }
}

.card,
.overlay-wrapper {
    // Box overlay for LOADING STATE effect
    >.overlay,
    >.loading-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .overlay {
        z-index: 50;
        background: rgba(255, 255, 255, 0.7);
        @include border-radius($border-radius);
        >.fa {
            position: absolute;
            top: 50%;
            left: 50%;
            margin-left: -15px;
            margin-top: -15px;
            color: #000;
            font-size: 30px;
        }
    }
    .overlay.dark {
        background: rgba(0, 0, 0, 0.5);
    }
}

// Add clearfix to header, body and footer
.card-header,
.card-body,
.card-footer {
    @include clearfix();
}

.card-inner-title {
    color: var(--primary-color);
    font-size: 16px;
    border-bottom: 1px solid $gray-300;
    padding-bottom: 10px;
    margin-bottom: 20px;
    font-weight: 500;
    @include media-breakpoint-up(md) {
        padding-bottom: 15px;
    }
    @include media-breakpoint-up(xxxl) {
        font-size: 20px;
        padding-bottom: 20px;
    }
}

// Box header
.card-header {
    position: relative;
    background-color: transparent;
    border-bottom: 1px solid $gray-200;
    padding: 0;
    .collapsed-card & {
        border-bottom: none;
    }
    >.card-tools {
        position: absolute;
        right: 1rem;
        top: 23px;
        [data-toggle="tooltip"] {
            position: relative;
        }
    }
    &.detailed-header{
        padding: 15px 20px;
        @include media-breakpoint-up(md) {
            padding: 20px 30px;
        }
        @include media-breakpoint-up(xxxl) {
            padding: 30px 40px;
        }
        &>div{
            &>div{
                margin-bottom: 20px;
                @include media-breakpoint-up(sm) {
                    margin-right: 30px;
                }
                @include media-breakpoint-up(md) {
                    margin-bottom: 0;
                }
                &:last-child{
                    margin-right: 0;
                }
                p{
                    margin-bottom: 10px;
                    color:$gray-700;
                }
                span{
                    color: $gray-700;
                }
                &.survey-heading{
                    max-width: 280px;
                }
            }
        }
        &.small-gap{
            &>div{
                &>div{
                    margin-right: 15px;
                    width: 280px;
                }   
            }
        }
        &.has-elements{
            &>div{
                &>div{
                   width: 100%;
                    @include media-breakpoint-up(sm) {
                        width: 40%;
                    }
                    @include media-breakpoint-up(md) {
                        width: 30%;
                    }
                }
            }
        }
    }
}

.card-footer {
    border-top: 1px solid $gray-200;
    padding: 15px 20px;
    @include media-breakpoint-up(md) {
        padding: 20px 30px;
    }
    @include media-breakpoint-up(xxxl) {
        padding: 30px 40px;
    }
}

.card-title {
    font-size: $card-title-font-size;
    font-weight: $card-title-font-weight;
    margin: 0;
}

// Box Tools Buttons
.btn-tool {
    padding: 0.25rem 0.5rem;
    font-size: $font-size-sm;
    background: transparent;
    color: $gray-500;
    .btn-group.show &,
    &:hover {
        color: $gray-700;
    }
    .show &,
    &:focus {
        box-shadow: none !important;
    }
}

// Box Body
.card-body {
    // @include border-radius-sides(0, 0, $border-radius, $border-radius);
    // Tables within the box body
    >.table {
        margin-bottom: 0;
        >thead>tr>th,
        >thead>tr>td {
            border-top-width: 0;
        }
    }
    // Calendar within the box body
    .fc {
        margin-top: 5px;
    }
    .full-width-chart {
        margin: -19px;
    }
    &.p-0 .full-width-chart {
        margin: -9px;
    }
}

