.left-card:after {
    position: absolute;
    content: "";
    right: 0;
    top: 0;
    bottom: 0;
    width: 1px;
    height: 100%;
    background: $gray-100;
    display: none;
    @include media-breakpoint-up(lg) {
        display: block;
    }
}
.btn.calendar-filter-btn{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 70;
    right: 0;
    border-radius: 0;
    width: 100%;
}
.popover {
    &.teal-theme{
        .popover-header {
            background-color: $teal;
        }
    }
    &.orange-theme{
        .popover-header {
            background-color: $orange;
        }
    }
    &.new-grey-theme{
        .popover-header {
            background-color: #a1a1a1;
        }
    }
    
    &.blue-theme{
        .popover-header {
            background-color: $blue;
        }
    }
    &.red-theme{
        .popover-header {
            background-color: $color-red;
        }
    }
    &.green-theme{
        .popover-header {
            background-color:  $color-green;
        }
    }
    &.yellow-theme{
        .popover-header {
            background-color: $color-yellow;
        }
    }
    &.light-gray-theme{
        .popover-header {
            background-color: $default-color;
        }
    }
    &.light-green-theme{
        .popover-header {
            background-color: $lime;
        }
    }
    &.indigo-theme{
        .popover-header {
            background-color: $indigo-theme;
        }
    }
}

.calendar-actions{
    a,
    button {
        font-size: 14px;
        width: 50%;
        text-align: center;
        padding: 8px 20px;
        @include media-breakpoint-up(md) {
            font-size: 16px;
            padding: 4px;
        }
        @include media-breakpoint-up(lg) {
            width: initial;
        }
        @include media-breakpoint-up(xxxl) {
            font-size: 18px;
        }
        &.calendar-filter-apply {
            border: 0;
            font-weight: 500;
            background: var(--primary-color);
            color: $white;
            cursor: pointer;
            outline: 0;
            &:hover {
                color: var(--primary-color-darken);
            }
            @include media-breakpoint-up(lg) {
                background: transparent;
                color: var(--primary-color);
            }
        }
    }
    .close-calendar-filter {
        color: #82808e;
        vertical-align: middle;
        background: $white;
        border: 1px solid $gray-200;
        @include media-breakpoint-up(lg) {
            border: 0;
        }
        &:hover {
            color: $gray-1000;
        }
    }
}
.calender-card{
    
    @include media-breakpoint-up(lg) {
        display: flex;
    }
    .calender-filter{
        position: fixed;
        background: white;
        bottom: 0;
        height: 100vh;
        left: 0;
        
        position: fixed;
        right: 0;
        z-index: 99;
        display: none;
        bottom: 0;
        @include media-breakpoint-up(lg) {
            width: 250px;
            position: static;
            background: transparent;
            overflow-y: inherit;
            display: block;
        }
        @include media-breakpoint-up(xl) {
            width: 320px;
        }
        @include media-breakpoint-up(xxxl) {
            width: 370px;
        }
        h5.filter-header{
            padding: 20px 15px;
            margin-bottom: 0;
            border-bottom: 1px solid #F4F5F7;
            @include media-breakpoint-up(xxxl) {
                padding: 30px 24px;
            }
        }
        .close-filter{
            position: absolute;
            right: 20px;
            top: 20px;
        }
        .filter-accordion{
            overflow-y: auto;
            height: calc(100vh - 99px);
            @include media-breakpoint-up(lg) {
                overflow-y: initial;
                height: initial;
            }
        }
    }
    .calender-wrap{
        width: 100%;
        padding: 15px;
        @include media-breakpoint-up(lg) {
            width: calc(100% - 250px);
            border-left: 1px solid #F4F5F7;
        }
        @include media-breakpoint-up(xl) {
            width: calc(100% - 320px);
        }
        @include media-breakpoint-up(xxxl) {
            width: calc(100% - 370px);
            padding: 35px;
        }
        // .fc table{
        //     border-collapse: separate;
        // }
        // .fc-widget-content:first-of-type, .fc-widget-header:first-of-type{
        //     border-left: 1px;
        //     border-right: 1px;
        // }
        .fc-header-toolbar h2{
            font-size: 22px;
        }
        .fc {
            .fc-daygrid-day-number{
                font-size: 18px;
                color: $gray-1000;
            }
            .fc-day-other .fc-daygrid-day-top {
                opacity: 1;
                .fc-daygrid-day-number{
                    opacity: 0.4;
                }
            }
            .fc-daygrid-day-bg .fc-non-business{
                background-color: $white;
            }
            .fc-button .fc-icon{
                height: auto;
                line-height: 1;
                font-size: inherit;
            }
        }
        .fc-icon-chevron-left:before {
            content: "\f104";
            font-weight: 400;
            font-size: 16px;
            position: relative;
            color: #666;
            font-family: $font-awesome-family;
        }
        .fc-icon-chevron-right:before {
            content: "\f105";
            font-weight: 400;
            font-size: 16px;
            position: relative;
            color: #666;
            font-family: $font-awesome-family;
        }
        .fc-icon-x:before {
            content: "\f00d";
            font-weight: 400;
            font-family: $font-awesome-family;
        }
        .fc button{
            border: 1px solid #D7D3C7;
            background: transparent;
            box-shadow: none;
            height: auto;
            padding: 5px 10px;
            font-weight: 500;
            font-family: 'Montserrat';
            text-transform: capitalize;
            color: $gray-900;
            font-size: 14px;
        }
        .fc .fc-button-primary:not(:disabled).fc-button-active{
            background: var(--primary-color);
            border-color: var(--primary-color);
            color: white;
            text-shadow: none;
            outline: none;
        }
        .fc-toolbar{
            padding: 0;
        }
        .fc-event {
            background-color: transparent;
            border: 0;
            border-radius: 20px;
            &.hidden {
                display: none;
            }
            img{
                border-radius: 100%;
            }
            &.light-green-event{
                background: #b2f8d1;
                .fc-event-title{
                    img{
                        border: 2px solid $lime;
                    }
                }
            }
            &.teal-event{
                background: #E4F3F6;
                .fc-event-title{
                    img{
                        border: 2px solid $teal;
                    }
                }
            }
            &[data-bs-custom-class="orange-theme"],&.orange-event{
                background: #FBE7E4;
                .fc-event-title{
                    img{
                        border: 2px solid $orange;
                    }
                }
            }
            &[data-bs-custom-class="new-grey-theme"],&.new-grey-event{
                background: #e3e3e3;
                .fc-event-title{
                    img{
                        border: 2px solid #a1a1a1;
                    }
                }
            }
            &[data-bs-custom-class="blue-theme"],&.blue-event{
                background: #b2e4f1;
                .fc-event-title{
                    img{
                        border: 2px solid $blue;
                    }
                }
            }
            &[data-bs-custom-class="red-theme"],&.red-event{
                background: #fcc7c2;
                .fc-event-title{
                    img{
                        border: 2px solid $color-red;
                    }
                }
            }
            &[data-bs-custom-class="green-theme"],&.green-event{
                background: #bcedde;
                .fc-event-title{
                    img{
                        border: 2px solid $color-green;
                    }
                }
            }
            &[data-bs-custom-class="yellow-theme"],&.yellow-event{
                background: #ffe6b2;
                .fc-event-title{
                    img{
                        border: 2px solid $color-yellow;
                    }
                }
            }
            &.light-gray-event{
                background: #f4f4f4;
                .fc-event-title{
                    img{
                        border: 2px solid $default-color;
                    }
                }
            }
        }
        // remove time for monthly view
        .fc-day-grid{
            .fc-time{
                display: none;
            }
        }
        .fc-agenda-view{
            .fc-event{
                padding: 15px;
            }
        }
        .fc-widget-header.fc-divider{
            padding: 0;
            border-bottom: 0;
        }
        .fc-widget-header.fc-axis {
            background: white;
        }
        .fc-daygrid-dot-event .fc-event-title{
            font-weight: 400;
        }
        .fc-daygrid-dot-event{
            padding: 0;
        }
        .fc-title{
            
            img{
                height: 26px;
                width: 26px;
                display: inline-block;
                border-radius: 100%;
                overflow: hidden;
                object-fit: cover;
            }
        }
        .fc-time-grid .fc-slats td{
            height: 2.5em;
        }
        thead {
            // background: $main-bg;
            th{
                height: 35px;
                line-height: 35px;
                text-transform: uppercase;
            }
        }
        .fc-view-container{
            border-left: 1px solid $gray-200;
            border-right: 1px solid $gray-200;
        }
        .fc-unthemed th, .fc-unthemed td, .fc-unthemed thead, .fc-unthemed tbody, .fc-unthemed .fc-divider, .fc-unthemed .fc-row, .fc-unthemed .fc-content, .fc-unthemed .fc-popover, .fc-unthemed .fc-list-view, .fc-unthemed .fc-list-heading td{
            border-color: $gray-200;
        }
        .fc-day-top.fc-other-month {
            opacity: 0.4;
        }
        .fc-ltr .fc-basic-view .fc-day-top .fc-day-number {
            font-size: 18px;
            font-weight: 500;
        }
        .fc-unthemed td.fc-today {
            // border-color: var(--primary-color);
            background: #FAF9FF;
            position: relative;
        }
        .fc-basic-view .fc-week-number, .fc-basic-view .fc-day-number {
            padding: 14px;
        }
         // remove time for weekly view
         .fc-agendaWeek-view{
            .fc-event{
                border-radius: 10px;
                padding: 4px;
            }
        }
    }
}
.form-card .wizard>.actions{
    background-color: $white;
}
.popover.popover-cal-event span {
    display: inline-block;
    min-width: 120px;
}