// map location styles

.map-section{
    background-color: $gray-400;
    height: 400px;
}
.location-table{
    tr{
        td{
            vertical-align: bottom;
            .action-icon{
                margin-bottom: 10px;
            }
        }
    }
    
}
.location-scrollbar{
    max-height: 360px;
}
.property-card{
    border:1px solid $gray-300;
}

.badge-label {
    padding: 16px 19px;
    border-radius: 20px;
    display: inline-flex;
    min-width: 165px;
    justify-content: space-between;
    margin-right: 30px;
    font-weight: 400;
    margin-bottom: 20px;
    align-items: center;
    span{
        vertical-align: middle;
    }
    i{
        margin-left: 10px;
        font-size: 22px;
        vertical-align: middle;
        height: 32px;
        width: 32px;
        line-height: 32px;
        border-radius: 100%;
        text-align: center;
        color: white;
    }
    &.badge-green {
        border: 2px solid #27857C;
        i{
            background-color: #27857C;
        }
    }
    
    &.badge-red {
        border: 2px solid #DC3545;
        i{
            background-color: #DC3545;
        }
    }
}

