/*
 * Component: Progress Bar
 * -----------------------
 */

//General CSS
.progress {
    @include box-shadow(none);
    @include border-radius(1px);
}

.progress-group {
    margin-bottom: 1rem;
}

// size variation
.progress-sm {
    height: 10px;
}

.progress-xs {
    height: 7px;
}

.progress-xxs {
    height: 3px;
}

// Vertical bars
.progress.vertical {
    position: relative;
    width: 30px;
    height: 200px;
    display: inline-block;
    margin-right: 10px;
    >.progress-bar {
        width: 100%;
        position: absolute;
        bottom: 0;
    }
    //Sizes
    &.sm,
    &.progress-sm {
        width: 20px;
    }
    &.xs,
    &.progress-xs {
        width: 10px;
    }
    &.xxs,
    &.progress-xxs {
        width: 3px;
    }
}

// Remove margins from progress bars when put in a table
.table {
    tr>td .progress {
        margin: 0;
    }
}
.progress {
    background: $gray-300;
    border-radius: 12px;
    height: 14px;
    .progress-bar {
        border-radius: 12px;
        background: $theme-gradiant;
    }
}
.progress.experience-score-bar {
    height: 35px;
    border-radius: 5px;
    margin-top: 0;
    overflow: visible;
    @include media-breakpoint-up(md) {
        margin-top: 50px;
        height: 40px;
        border-radius: 10px;
    }
    @include media-breakpoint-up(xxxl) {
        margin-top: 70px;
        height: 50px;
    }
    &.progress {
        .progress-bar {
            position: relative;
            white-space: initial;
            line-height: 1.2;
            border-radius: 0;
            background: $gray-300;
            font-size: 13px;
            @include media-breakpoint-up(xxxl) {
                font-size: 14px;
                font-weight: 500;
            }
            &:first-child {
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
                @include media-breakpoint-up(md) {
                    border-top-left-radius: 10px;
                    border-bottom-left-radius: 10px;
                }
            }
            &:last-child {
                border-bottom-right-radius: 5px;
                border-top-right-radius: 5px;
                @include media-breakpoint-up(md) {
                    border-bottom-right-radius: 10px;
                    border-top-right-radius: 10px;
                }
            }
            &::before {
                position: absolute;
                content: "";
                top: -45px;
                height: 30px;
                width: 30px;
                z-index: 2;
                border-radius: 100%;
                left: 0;
                right: 0;
                text-align: center;
                margin: 0 auto;
                background-repeat: no-repeat;
                background-position: center;
                background-size: 16px;
                display: none;
                @include media-breakpoint-up(md) {
                    top: -55px;
                    height: 40px;
                    width: 40px;
                    background-size: 20px;
                    display: inline-block;
                }
                @include media-breakpoint-up(xxxl) {
                    top: -65px;
                    height: 50px;
                    width: 50px;
                    background-size: 25px;
                }
            }
            &.small-bar-size {
                &:before {
                    right: 0;
                    left: auto;
                }
            }
            &.teal {
                background-color: #50C9B5;
                &:before {
                    background-color: #50c9b5;
                    background-image: url(../img/icons/strongly-agree.svg);
                }
            }
            &.lime {
                background-color: #C9DD03;
                color: #675C53;
                &:before {
                    background-color: #C9DD03;
                    background-image: url(../img/icons/agree.svg);
                }
            }
            &.orange {
                background-color: #FCB515;
                color: #675C53;
                &:before {
                    background-color: #FCB515;
                    background-image: url(../img/icons/neither.svg);
                }
            }
            &.orange-dark {
                background-color: #E77D20;
                &:before {
                    background-color: #E77D20;
                    background-image: url(../img/icons/disagree.svg);
                }
            }
            &.red {
                background-color: #F16469;
                &:before {
                    background-color: #F16469;
                    background-image: url(../img/icons/strongly-disagree.svg);
                }
            }
        }
    }
}

.experience-score-legend {
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    flex-wrap: wrap;
    li {
        padding-left: 15px;
        position: relative;
        margin-right: 10px;
        font-size: 11px;
        &:before {
            position: absolute;
            content: "";
            left: 0;
            top: 3px;
            width: 8px;
            height: 8px;
            background-color: $gray-200;
            border-radius: 100%;
        }
        &.teal {
            &:before {
                background-color: #50c9b5;
            }
        }
        &.lime {
            &:before {
                background-color: #C9DD03;
            }
        }
        &.orange {
            &:before {
                background-color: #FCB515;
            }
        }
        &.orange-dark {
            &:before {
                background-color: #E77D20;
            }
        }
        &.red {
            &:before {
                background-color: #F16469;
            }
        }
    }
}

