// Globle start
img {
    max-width: 100%;
}

.mm-w-100 {
    min-width: 100px;
}

.mm-h-80 {
    min-height: 80vh;
}

.o-l-n {
    outline: none;
}

/* Text-color */
.text-light-orange {
    color: $light-orange;
}

.color-yellow {
    color: #ffab00;
}

.color-green {
    color: #21c393;
}

.bg-green {
    background-color: #21c393;
}

.bg-yellow {
    background-color: #ffab00;
}

.bg-red {
    background-color: #f44436;
}

 // font classes
 @for $i from 0 through 40 {
    .font-#{$i * 1} {
        font-size: #{$i * 1}px;
    }
}
.gray-100 {
    color: $gray-100;
}

.gray-200 {
    color: $gray-200;
}

.gray-300 {
    color: $gray-300;
}

.gray-400 {
    color: $gray-400;
}

.gray-500 {
    color: $gray-500;
}

.gray-600 {
    color: $gray-600;
}

.gray-700 {
    color: $gray-700;
}

.gray-800 {
    color: $gray-800;
}

.gray-900 {
    color: $gray-900;
}
.gray-1000 {
    color: $gray-1000;
}
// margin bottom vh calsses
@for $i from 0 through 30 {
    .m-b-vh-#{$i * 1} {
        margin-bottom: #{$i * 1}vh;
    }
}

/* bg color */
.bg-gray-1 {
    background: $gray-100;
}

.text-break {
    word-break: break-word !important;
    overflow-wrap: break-word !important;
}

@each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
        $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

        .border#{$infix}-right {
            border-right: 1px solid $gray-200 !important;
        }

        .border#{$infix}-left {
            border-left: 1px solid $gray-200 !important;
        }
    }
}

.max-w-250 {
    max-width: 250px;
    margin: 0 auto;
}
.img-rounded{
    border-radius: 10px;
    overflow: hidden;
}