.custom-radio {
  display: block;
  position: relative;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  border-color: transparent;
  border-width: 1px;
  border-style: solid;
  color: #495057;
  min-height: calc(1.5em + 0.75rem + calc(var(--bs-border-width) * 2));
  padding: 12px 20px;
  line-height: 1.5;
  margin-right: 0.8rem;
  margin-top: 5px;
}
.custom-radio:last-child {
  margin-right: 0;
}
