.custom-radio input {
  position: absolute;
  opacity: 0 !important;
  cursor: pointer;
  height: 0;
  width: 0;
  visibility: hidden;
}
.custom-radio .checkmark {
  position: absolute;
  top: 0;
  right: 0;
  height: 24px;
  width: 24px;
  background-color: #eee;
  border-radius: 50%;
  margin-top: -6px;
  margin-right: -6px;
  z-index: 2;
  opacity: 0;
}
.custom-radio:hover input ~ .checkmark {
  background-color: #ccc;
}
.custom-radio input:checked ~ .checkmark {
  background-color: var(--primary-color);
  opacity: 1;
}
.custom-radio .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.custom-radio input:checked ~ .checkmark:after {
  display: block;
}
.custom-radio .checkmark:after {
  left: 8px;
  top: 4px;
  width: 6px;
  height: 12px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  content: "";
  position: absolute;
}
.custom-radio .box-line:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-color: #E0E1E6;
  border-width: 1px;
  border-style: solid;
  border-radius: 10px;
