@-webkit-keyframes loader-ripple {
  0% {
    top: 98px;
    left: 98px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 19px;
    left: 19px;
    width: 158px;
    height: 158px;
    opacity: 0;
  }
}
@-moz-keyframes loader-ripple {
  0% {
    top: 98px;
    left: 98px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 19px;
    left: 19px;
    width: 158px;
    height: 158px;
    opacity: 0;
  }
}
@keyframes loader-ripple {
  0% {
    top: 98px;
    left: 98px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 19px;
