  }
}
.lds-ripple {
  position: relative;
  margin: 0 auto;
  width: 119px !important;
  height: 119px !important;
  -webkit-transform: translate(-59.5px, -59.5px) scale(0.595) translate(59.5px, 59.5px);
  transform: translate(-59.5px, -59.5px) scale(0.595) translate(59.5px, 59.5px);
}
.lds-ripple div {
  box-sizing: content-box;
  position: absolute;
  border-width: 2px;
  border-style: solid;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: loader-ripple 1.9s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  animation: loader-ripple 1.9s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(1) {
  border-color: var(--primary-color);
}
.lds-ripple div:nth-child(2) {
  border-color: #5261ac;
  -webkit-animation-delay: -0.95s;
  animation-delay: -0.95s;
}

