.recipe-like-wrap{
    font-size: 16px;
    @include media-breakpoint-up(xxxl){
        font-size: 18px;
    }
    
}
#recipeCarousel.owl-carousel .owl-item{
    border-radius: 10px;
    overflow: hidden;
    img{
        height: 290px;
        object-fit: cover;
        @include media-breakpoint-up(xxxl){
            height: 360px;
        }
    }
}
.form-group textarea.form-control.recipe-textarea {
    height: 290px;
    line-height: 1.4;
}
.ingredients-table{
    max-height: 290px;
    overflow-y: auto;
}
.nutrition-block {
    border: 1px solid $gray-200;
    padding: 20px;
    border-radius: 10px;
    @include media-breakpoint-up(lg){
        display: flex;
        justify-content: space-between;
    }
    span{
        display: block;
        min-width: 80px;
        text-align: left;
        margin-left: auto;
        @include media-breakpoint-up(lg){
            margin-top: 5px;
            margin-left: initial;
        }
    }
    div{
        display: flex;
        align-items: center;
        max-width: 300px;
        @include media-breakpoint-up(lg){
            max-width: initial;
            display: block;
        }
        &:not(:last-child){
            margin-bottom: 15px;
            @include media-breakpoint-up(lg){
                padding-right: 15px;
                margin-bottom: 0;
            }
        }
    }
}
.remove-image{
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 4px 8px;
    border-radius: 6px;
    i{
        font-size: 15px;
    }
}


.ingriadiant-make-editable tbody tr:last-child td.show_del .ingriadiant-remove { display: block !important; }
.ingriadiant-make-editable tbody tr:last-child td.show_del #ingriadiantAdd { display: none !important; }

