// new event page
.event-row-list{
    > div{
        &:nth-child(2n){
            margin-bottom: 25px;
        }
        
    }
}

// check class .seperator-block for static block of same type
.picked-time-info{
    border:1px solid var(--primary-color);
    border-radius: 20px;
    padding:20px;
    display: flex;
    color: $gray-1000;
    max-width: 250px;
    justify-content: space-between;
}
.presenter-owl-carousel{
    .custom-radio{
        width: 100%;
        text-align: center;
        padding:10px;
        margin-bottom: 0;
        hr{
            margin: 10px -10px;
        }
    }
    .owl-item{
        padding-right: 10px;
    }
}
.presenter-item-container{
    display: flex;
    flex-wrap: wrap;
}
.presenter-item{
    border: 1px solid $gray-300;
    border-radius: 10px;
    width: 33.33%;
    .presenter-top{
        display: flex;
        padding: 10px;
        border-bottom: 1px solid $gray-300;
        align-items: center;
        flex-wrap: wrap;
        .presenter-top-left{
            display: flex;
            border-right: 1px solid $gray-300;
            margin-right: 15px;
            align-items: center;
            padding-right: 15px;
            flex: 0.95;
            img{
                height: 40px;
                width: 40px;
                object-fit: cover;
                border-radius: 100%;
                border: 3px solid $gray-300;
                margin-right: 15px;
            }
        }
        .presenter-name{
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
    }
    .presenter-bottom{
        padding: 10px;
        .custom-radio{
            margin-right: 4px;
            padding: 4px 6px;
            min-height: auto;
            font-size: 12px;
            border-radius: 4px;
            width: 75px;
            margin-bottom: 4px;
            @include media-breakpoint-up(xxxl) {
                padding: 5px 8px;
                margin-right: 6px;
                font-size: 13px;
                width: auto;
            }
            .box-line{
                &:before{
                    border-color: $green;
                    border-radius: 4px;
                }
            }
            &.disabled{
                background-color: $gray-400;
                opacity: 0.7;
                color: $gray-600;
                pointer-events: none;
                .box-line{
                    &:before{
                        border-color: $gray-400;
                    }
                }
            }
            &.checked{
                background: var(--primary-color);
                border-radius: 4px;
                color: white;
                .box-line{
                    &:before{
                        border-color: var(--primary-color);
                    }
                }
            }
        }
        .view-more{
            font-size: 12px;
            @include media-breakpoint-up(xxxl) {
                font-size: 13px;
            }
        }
        .other-timings{
            display: none;
        }
    }
}
.arrow-theme{
    padding-right: 60px;
    padding-left: 60px;
    &.owl-theme{
        .owl-nav [class*=owl-]{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            box-shadow: 0px 3px 30px 0px rgba(0,0,0,0.2);
            line-height: 36px;
            height: 36px;
            width: 36px;
            border-radius: 100%; 
            font-size: 18px;
            &:hover{
                background-color: transparent;
                color: inherit;
            }
            &:focus{
                outline: 0;
            }
        }
        .owl-prev{
            left: 0;
        }
        .owl-next{
            right: 0;
        }
    }
}


.slot-disabled{
    background: #f5f5f5;
    padding: 15px;
    pointer-events: none;
}