//------ create question ------//
// ---------------------------------------------------
@import "variables";
@import "bootstrap-variables";
.question-wrap {
    &+.question-wrap {
        padding-top: 50px;
        margin-top: 50px;
        border-top: 1px solid #D4D2DE;
    }
    .question-offset {
        margin-left: 30px;
        margin-top: 30px;
    }
    .question-group-outer {
        // min-width: 400px;
        display: flex;
        margin-bottom: 20px;
    }
}

.question-group {
    select.form-control {
        background-image: linear-gradient(45deg, transparent 50%, #675C53 50%), linear-gradient( 135deg, #675C53 50%, transparent 50%);
        background-position: calc(100% - 20px) calc(1em + 4px), calc(100% - 15px) calc(1em + 4px), calc(100% - 2.5em) 0.5em;
        background-size: 5px 5px, 5px 5px, 1px 1.5em;
        background-repeat: no-repeat;
        -webkit-appearance: none;
        -moz-appearance: none;
    }
    &>div:first-child{
        display: inline-block!important;
        width: 105px;
    }
    .input-group-append {
        width: calc(100% - 70px);
    }
    .custom-file-preview .file-preview-img {
        right: 0;
        left: initial;
    }
    .custom-file-preview .custom-file-label {
        padding: 12px 20px;
        padding-right: 62px;
        &:after {
            display: none;
        }
    }
    .custom-file-preview .custom-file-label {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        border-left: 1px solid $gray-300;
    }
    .cu-tooltip-wrap{
        &:hover{
            .cu-tooltip{
                display: block;
            }
        }
    }
}

.shortAnswersContent {
    .question-wrap {
        &+.question-wrap {
            padding-top: 30px;
            margin-top: 0;
            border-top: 0;
        }
    }
}

