// DatePicker
.input-group-addon {
    padding: 7px 10px;
}
.custom-file-preview {
    .custom-file-input {
        cursor: pointer;
    }
    .custom-file-label {
        border-top-left-radius: 45px;
        border-bottom-left-radius: 45px;
        padding-left: 55px;
    }
    .file-preview-img {
        position: absolute;
        left: 0;
        margin-top: -5px;
        z-index: 9;
        background: #fff;
        border-radius: 45px;
        box-shadow: 0 0 0px 3px #ced4da;
        height: 45px;
        width: 45px;
        text-align: center;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        justify-items: center;
        img {
            max-height: 45px;
            max-width: 45px;
            width: auto;
            height: auto;
        }
    }
}
.cu-alert-success {
    background: var(--primary-color) !important;
    color: $white !important;
    padding: 8px 12px;
    margin: 15px 0 0;
    text-shadow: none;
}

.full-screen-popup .modal-header {
    margin-bottom: 0;
}

.question-text-title {
    font-size: 1rem;
}


.input-group>.custom-file .custom-file-input:focus {
    z-index: 99;
}
// --------------------------  Ratting Area ------------------------------
.cu-tooltip-wrap {
    &:hover {
        .cu-tooltip {
            display: block;
        }
    }
    .cu-tooltip {
        max-width: 200px;
        padding: 7px 8px;
        color: #fff;
        text-align: center;
        background-color: #343a40;
        border-radius: 0.25rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: none;
        top: 100%;
        line-height: 1;
        font-size: 12px;
        min-width: 100px;
        opacity: 0.9;
        margin-top: 7px;
        white-space: initial;
        z-index: 9;
        &::before {
            position: absolute;
            content: '';
            top: -5px;
            left: 50%;
            margin: 0 auto;
            border-color: transparent transparent #343a40;
            border-style: solid;
            border-width: 0 8px 8px;
            margin-left: -8px;
        }
    }
}

//round-score
.round-score {
    display: inline-block;
    border-radius: 100px;
    min-width: 50px;
    color: #fff;
    padding: 5px 10px;
}

// new code
.chart-wrap {
    position: relative;
    .chart-value {
        color: #f44436;
        position: absolute;
        bottom: -15px;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 12px;
        &.chart-value-lg {
            font-size: 16px;
            font-weight: 600;
        }
    }
}

.chart-graph {
    max-width: 100% !important;
    // height: auto !important;
}

.chart-graph-lg {
    width: 100% !important;
    // height: auto !important;
}

.chart-badge {
    background-color: #f44436;
    color: white;
    border-radius: 27px;
    border-radius: 27px;
    padding: 9px 30px;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 14px;
    margin-top: 20px;
    white-space: normal;
    @include media-breakpoint-up(xxxl) {
        font-size: 16px;
    }
}
.img-upload-area{
    position: absolute;
    right: 0;
    margin-top: 0;
    top: 0;
    // z-index: 9;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 0 2px #ced4da;
    height: 45px;
    width: 45px;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    // overflow: hidden;
    input[type=file] {
        position: absolute;
        top: 0;
        left: 0;
        text-align: right;
        filter: alpha(opacity=0);
        opacity: 0;
        outline: 0;
        cursor: pointer;
        right: 0;
        bottom: 0;
        height: 45px;
        width: 45px;
    }
    img{
        max-height:45px;
    }
    label{
        border-radius: 5px;
        overflow: hidden;
        height:45px;
        width:45px;
        line-height: 42px;
    }
}
.choices-option-input{
    width: calc(100% - 105px);
    .form-control{
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        padding-right: 60px;
    }
}
.qus-inline{
    position: relative;   
}
.qus-btn-area{
    a{
        margin-top: 12px;
        margin-left: 15px;
    }
}
.qus-inline-choice,.qus-inline-free-text{
    display: flex;
    .question-text-span-choice,.question-text-span-free-text{
        max-width: 80%;
    }
    .qus-btn-area{
        flex-shrink: 0;
        align-self: flex-start;
        margin-top: -1px;
        a{
            vertical-align: middle;
        }
    }
    .qus-main-img-upload{
        position: relative;
        z-index: 10;
    }
}
.question-group-outer{
    .question-group select.form-control{
        height: 39.5px;
        
        border-right: 1px solid $gray-300;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        @include media-breakpoint-up(lg) {
            height: 44px;
        }
    }
}
::-webkit-file-upload-button{
    cursor: pointer;
}
