/*
 * Plugin: Select2
 * ---------------
 */

//Signle select
.select2-container--default,
.select2-selection {
    &.select2-container--focus,
    &:focus,
    &:active {
        outline: none;
    }
    .select2-selection--single {
        border: 1px solid $gray-300;
        //border-radius: $input-radius;
        padding: 6px 12px;
    }
}

.select2-container--default.select2-container--open {
    border-color: theme-colors("primary");
}

.select2-dropdown {
    z-index: 1056;
    border: 1px solid $gray-300;
    //border-radius: $input-radius;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
    background-color: map-get($theme-colors,"primary");
    color: white;
}

.select2-results__option {
    padding: 6px 12px;
    user-select: none;
    -webkit-user-select: none;
}

.select2-container .select2-selection--single .select2-selection__rendered {
    padding-left: 0;
    padding-right: 0;
    height: auto;
}

.select2-container[dir="rtl"] .select2-selection--single .select2-selection__rendered {
    padding-right: 6px;
    padding-left: 20px;
}

.select2-container--default .select2-selection--single,
.select2-selection .select2-selection--single {
    padding: 10px 15px;
    height: initial;
    border-color: $gray-300;
    border-radius: 6px;
    @include media-breakpoint-up(lg) {
        padding: 12px 20px;
    }
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: auto;
    margin-top: -3px;
    top: 50%;
    right: 15px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
    margin-top: 0;
    color: $gray-900;
}

.select2-dropdown,
.select2-search--inline {
    .select2-search__field {
        border: 1px solid $gray-300;
        &:focus {
            outline: none;
            border: 1px solid map-get($theme-colors,"primary");
        }
    }
}

.select2-container--default .select2-results__option[aria-disabled=true] {
    color: #999;
}

.select2-container--default .select2-results__option[aria-selected=true] {
    background-color: #ddd;
    &,
    &:hover {
        color: #444;
    }
}

//Multiple select
.select2-container--default {
    .select2-selection--multiple {
        border: 1px solid $gray-300;
        //border-radius: $input-radius;
        &:focus {
            border-color: map-get($theme-colors,"primary");
        }
    }
    &.select2-container--focus .select2-selection--multiple {
        border-color: $gray-300;
    }
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
    background-color: map-get($theme-colors,"primary");
    border-color: shade-color($primary, 5%);
    padding: 1px 10px;
    color: $white;
    margin-top: 8px;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    margin-right: 5px;
    color: rgba(255, 255, 255, 0.7);
    &:hover {
        color: $white;
    }
}

.select2-container .select2-selection--single .select2-selection__rendered {
    padding-right: 15px;
    height: auto;
    line-height: initial;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
    color: $input-color
}

.select2-container--default .select2-selection--single .select2-selection__placeholder {
    color: $input-placeholder-color;
    font-weight: 400;
}

/*.form-group .select2-container--default.select2-container--open.select2-container--below.select2-container--open .select2-selection--single,
.form-group .select2-container--default.select2-container--open .select2-selection--multiple {
    box-shadow: none;
    border-color: $input-placeholder-color;
}*/


/* multiple */

.select2-container .select2-selection--multiple {
    min-height: 44px;
    border-color: $gray-300;
    border-radius: 6px;
}

.select2-container--default .select2-search--inline .select2-search__field {
    width: 0.75em;
    height: initial;
    margin-top: 3px;
    border: none !important;
    padding: 8px 15px 4px;
    opacity: 0.4;
}

.select2-container--default.select2-container--focus .select2-search--inline .select2-search__field {
    opacity: 1;
}

.select2-container--default .select2-selection--multiple .select2-selection__clear {
    position: absolute;
    top: 7px;
    right: 0;
}

.select2-container .select2-search--inline {
    display: block;
    width: auto;
    float: none;
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered {
    padding: 0 5px;
}

.select2-container--default.select2-container--open.select2-container--below.select2-container--open .select2-selection--single,
.select2-container--default.select2-container--open .select2-selection--multiple {
    color: #495057;
    background-color: #ffffff;
    border-color: #53dcff;
    outline: 0;
    box-shadow: inset 0 0 0 rgba(52, 58, 64, 0), 0 0 0 0.2rem rgba(0, 167, 210, 0.25);
}

.form-group .select2-container--default.select2-container--open.select2-container--below.select2-container--open .select2-selection--single,
.form-group .select2-container--default.select2-container--open .select2-selection--multiple {
    background-color: $white;
}