/*
 * Component: Callout
 * ------------------
 */

// Base styles (regardless of theme)
.callout {
    @if $enable-rounded {
        @include border-radius($border-radius-lg);
    }
    @if $enable-shadows {
        @extend .elevation-1;
    }
    @else {
        border: 1px solid $gray-200;
    }
    @extend .mb-3;
    background-color: $white;
    padding: 10px 20px;
    border: 1px solid $gray-200;
    border-left: 5px solid $gray-300;
    a {
        color: $white;
        text-decoration: underline;
        &:hover {
            color: #eee;
        }
    }
    p:last-child {
        margin-bottom: 0;
    }
    b {
        display: inline-block;
        vertical-align: middle;
    }
    // Themes for different contexts
    &.callout-danger {
        border-left-color: shade-color($danger, 10%);
    }
    &.callout-warning {
        border-left-color: shade-color($warning, 10%);
    }
    &.callout-info {
        border-left-color: shade-color($info, 10%);
    }
    &.callout-success {
        border-left-color: shade-color($success, 10%);
    }
}