/**
 * Component: Sidebar
 * ------------------
 */

.main-sidebar {
    z-index: 1001;
    height: 100vh;
    overflow-y: hidden;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background: var(--primary-color);
    @include transition(margin-left .3s ease-in-out,width .3s ease-in-out);
    width: $sidebar-width;
    margin-left: -$sidebar-width;
    @include media-breakpoint-up(lg) {
        margin-left: 0;
    }
    @include media-breakpoint-up(xxxl) {
        width: $sidebar-width-xxl;
    }
    .sidebar-collapse & {
        margin-left: -$sidebar-width;
        @include media-breakpoint-up(xxxl) {
            margin-left: -$sidebar-width-xxl;
        }
    }
    .sidebar-open & {
        margin-left: 0;
    }
    .sidebar {
        overflow-x: hidden;
        overflow-y: auto;
        height: 100%;
        nav {
            height: 100%;
            &:not(.mCS_no_scrollbar){
                .nav-sidebar .treeview-arrow{
                    right: 5px;
                }
            }
        }
    }
}


// Sidebar navigation menu
.nav-sidebar {
    // All levels
    &>.nav-item {
        margin-bottom: 15px;
        >.nav-link {
            //update line 174 -- hamburger padding accordingly
            padding: 0.5rem 1.2rem 0.5rem 1.0rem;
            color: $gray-300;
            position: relative;
            @include media-breakpoint-up(xxxl) {
                padding: 0.7rem 1.5rem;
            }
            span,i.menu-icon {
                margin-right: 6px;
                font-size: 17px;
                vertical-align: middle;
                min-width: 23px;
                display: inline-block;
                text-align: center;
            }
            // &::before {
            //     position: absolute;
            //     content: "";
            //     left: 0;
            //     top: 0;
            //     bottom: 0;
            //     width: 4px;
            //     background-color: $white;
            //     opacity: 0;
            //     @include media-breakpoint-up(xxxl) {
            //         width: 5px;
            //     }
            // }
            &.active,&:hover,&:focus,&.menuactive {
                color: $white;
                background: darken($primary, 4%);
                // &:before {
                //     opacity: 1;
                // }
                // ~.nav-treeview {
                //     &:before {
                //         opacity: 1;
                //     }
                // }
            }
            &.active,&.menuactive{
                box-shadow: inset 4px 0 0 #fff;
            }
        }
        &.menu-open{
            >.nav-treeview {
                // display: block;
                // .sidebar-collapse &{
                //     display: none;
                // }
                // .sidemenu-hovered &{
                //     display: block !important;
                // }
            }
            >.nav-link{
                color: $white;
                background: darken($primary, 4%);
                box-shadow: inset 4px 0 0 #fff;
                .sidebar-collapse &{
                    color: $gray-300;
                    background:transparent;
                    box-shadow: none;
                }
            }
        }
        // First Level
        .nav-item {
            p {
                font-size: 13px;
            }
            .nav-icon {
                font-size: 8px;
                margin: 0;
                width: 8px;
            }
        }
    }
    .nav-item {
        >.nav-link {
            color: $gray-300;
            &.active,&.menuactive{
                color: $white;
                background: darken($primary, 4%);
            }
            &:hover{
                color: $white;
                background: darken($primary, 3%);
            }
        }
    }
    .treeview-arrow {
        position: absolute;
        right: 20px;
        top: 12px;
        transform: rotate(0deg);
        @include media-breakpoint-up(xxxl) {
            top: 17px;
        }
    }
    .nav-link {
        p {
            transition: margin-left .3s linear,opacity .5s ease;
            display: inline-block;
            margin: 0;
            vertical-align: middle;
            font-size: 13px;
        }
    }
    .menu-open {
        .treeview-arrow {
            transform: rotate(-180deg);
        }
    }
    // Tree view menu
    .nav-treeview {
        display: none;
        list-style: none;
        padding: 0;
        padding-left: 0.5rem;
        position: relative;
        .nav-link {
            padding-left: 33px;
            @include media-breakpoint-up(xxxl) {
                padding-left: 43px;
            }
        }
        &:before {
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            bottom: 0;
            width: 4px;
            background-color: $white;
            opacity: 0;
        }
    }
}
//hover
.sidebar-collapse {
    @include media-breakpoint-up(lg) {
         // Apply the new margins to the main content and footer
         .content-wrapper,
         .main-footer {
             margin-left: $sidebar-mini-width !important;
             @include media-breakpoint-up(xxxl){
                 margin-left: $sidebar-mini-width-xxl;
             }
         }
         .nav-sidebar .nav-link p{
             opacity: 0;
             margin-left: -10px;
         }
         // Modify the sidebar to shrink instead of disappearing
         .main-sidebar {
             margin-left: 0;
             width: $sidebar-mini-width;
             @include media-breakpoint-up(xxxl){
                 width: $sidebar-mini-width-xxl;
             }
         }
        .main-sidebar:hover {
            width: $sidebar-width;
            &:hover+.content-wrapper,
            &:hover+.content-wrapper+.main-footer {
                margin-left: $sidebar-width !important;
            }
            .nav-sidebar .nav-link p {
                opacity: 1;
                margin-left: 0;
                display: inline-block;
            }
        }
        .nav-sidebar,
        .nav-sidebar>.nav-header,
        .nav-sidebar .nav-link {
            overflow: hidden;
            white-space: nowrap;
        }
    }
}

#sidebar-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 1000;
}