// Card main
//------------------------------------------
.collapsed-card,
.card-header.collapsed {
    .fa-chevron-up {
        @include transform(rotate(180deg));
    }
}

.accordion {
    .card {
        margin-bottom: 0rem !important;
        border-color: $input-border-color;
        border-width: $input-border-width;
        border-style: solid;
        box-shadow: none;
        
        .card-header.collapsed {
            border-bottom-color: transparent;
        }
        
    }
    &.filter-accordion{
        .card {
            border: none;
            box-shadow: none !important;
            border-radius: 0;
            &:after{
                position: absolute;
                left: 20px;
                right: 20px;
                content: "";
                background: #f5f7f7;
                height: 1px;
                bottom: 0;
            }
            .card-header{
                border: none;
            }
            .btn:not(:disabled):not(.disabled).active:focus, .btn:not(:disabled):not(.disabled):active:focus{
                box-shadow: none;
            }
            .btn{
                padding: 10px 15px;
                @include media-breakpoint-up(xxxl) {
                    padding: 16px 23px;
                }
                width: 100%;
                text-align: left;
                &:after{
                    position: absolute;
                    content: "\f106";
                    font-family: $font-awesome-family;
                    right: 20px;
                    color: #aca9b9;
                }
                &.collapsed{
                    &:after{
                        content: "\f107";
                    }
                }
            }
            .card-body{
                padding: 0 15px 10px;
                @include media-breakpoint-up(xxxl) {
                    padding: 0 23px 16px;
                }
            }
           
        }
    }
}
.search-wrap{
    position: relative;
    .form-control{
        padding-right: 35px;
        border-radius: 22px;
    }
    i{
        position: absolute;
        right: 15px;
        top:11px;
    }
}
.filter-list {
    padding: 0;
    list-style: none;
    margin-bottom: 0;
    &.custom-scrollbar{
        height: 410px;
    }
    li {
       
        width: 100%;
        padding: 4px 5px;
        &:hover{
            background-color: $gray-200;
        }
        label.custom-checkbox{
            width: 100%;
            display: flex;
            align-items: center;
            padding-left: 0;
            padding-right: 1.5rem;
            .checkmark{
                left: auto;
                right: 0;
                top: 50%;
                width: 18px;
                height: 18px;
                margin-top: -9px;
                &:after{
                    height: 11px;
                    width: 6px;
                }
            }
        }
        &:last-child {
            border-bottom: 0;
        }
        .sx-user-img {
            width: 20px;
            height: 20px;
            border-radius: 100%;
            margin-right: 15px;
            @include media-breakpoint-up(xxxl) {
                width: 30px;
                height: 30px;
            }
            img {
                width: 100%;
                height: 100%;
                border-radius: 100%;
                object-fit: cover;
                vertical-align: top;
            }
        }
        .sx-color{
            width: 19px;
            height: 19px;
            border-radius: 3px;
            margin-right: 15px;
            &.teal{
                background-color: $teal;
            }
            &.orange{
                background-color: $orange;
            }
            &.new-grey{
                background-color: #a1a1a1;
            }
            &.blue{
                background-color: $blue;
            }
            &.red{
                background-color: $color-red;
            }
            &.green{
                background-color: $color-green;
            }
            &.yellow{
                background-color: $color-yellow;
            }
            &.light-gray{
                background-color: $default-color;
            }
            &.indigo{
                background-color: $indigo-theme;
            }
            &.light-green{
                background-color: $lime;
            }
        }
        .sx-list-name {
            color: $gray-1000;
            margin-bottom: 0px;
            line-height: 1.4;
            margin-bottom: 0;
            font-size: 12px;
        
            @include media-breakpoint-up(xxxl) {
                font-size: 14px;
            }
        }
        .sx-list-hr {
            margin-left: auto;
            color: #a1a1a1;
        }
    }
}
.ck-editor__editable {
    min-height: 200px;
}
.modal .ck .ck-sticky-panel .ck-sticky-panel__content_sticky{ 
    position: static !important; 
}
.input-group.symbol-end{
    flex-direction: row-reverse;
    &.input-group>:not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(.valid-feedback):not(.invalid-tooltip):not(.invalid-feedback){
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        margin-left: 0;
        margin-right: -1px;
    }
    &.input-group:not(.has-validation)>:not(:last-child):not(.dropdown-toggle):not(.dropdown-menu):not(.form-floating){
        border-radius: 4px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
}
//Avatar 

.edit-profile-wrapper {
    position: relative;
    cursor: pointer;
    width: 180px;
    @include media-breakpoint-up(lg) {
        width: 220px;
    }
    @include media-breakpoint-up(xxxl) {
        width: 260px;
    }
    .profile-image {        
        display: block;
        height: 180px;
        width: 180px;
        border: 1px solid #ABB0CB;
        border-radius: 9px;
        overflow: hidden;
        @include media-breakpoint-up(lg) {
            height: 220px;
            width: 220px;
        }
        @include media-breakpoint-up(xxxl) {
            height: 260px;
            width: 260px;
        }
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    .edit-profile-avtar {
        margin-top: 15px;
        font-size: 14px;
        color: var(--primary-color);
        text-align: center;
        cursor: pointer;
        overflow: hidden;
        position: relative;
        .edit-avtar-icon {
            fill: white;
            position: relative;
            height: 20px;
            width: 20px;
            top: 8px;
            left: 1px;
        }

        input[type=file] {
            position: absolute;
            top: 0;
            left: 0;
            text-align: right;
            filter: alpha(opacity=0);
            opacity: 0;
            outline: 0;
            cursor: pointer;
            right: 0;
            bottom: 0;
        }
    }
    &.edit-profile-small{
        width: 155px;
        @include media-breakpoint-up(lg) {
            width: 165px;
        }
        .profile-image {        
            width: 155px;
            height: 155px;
            @include media-breakpoint-up(lg) {
                width: 165px;
                height: 165px;
            }
        }
    }
}

