.partition-line{
    position: relative;
    margin: 35px 0 15px;
    &:after {
        position: absolute;
        content: "";
        left: 0;
        height: 1px;
        width: 100%;
        background: $gray-400;
        top: 50%;
    }
    span{
        background: $white;
        padding: 0 10px;
        text-transform: uppercase;
        position: relative;
        z-index: 2;
        color: $gray-600;
    }
}
