.otp-modal{
    p{
     font-size: 16px;
     @include media-breakpoint-up(lg) {
         font-size: 18px;
     }
    }
    .otp-inner-width{
         max-width: 270px;
         margin: 30px auto 0;
         text-align: left;
         @include media-breakpoint-up(lg) {
             max-width: 310px;
             margin: 20px auto 0;
         }
    }
    .digit-group {
        display: flex;
        justify-content: center;
        input {
            width: 38px;
            height: 38px;
            line-height: 38px;
            text-align: center;
            font-size: 18px;
            font-family: 'Raleway', sans-serif;
            margin: 0 2px;
            @include media-breakpoint-up(lg) {
                font-size: 20px;
                width: 44px;
                height: 44px;
                line-height: 44px;
            }
            @include media-breakpoint-up(xl) {
                font-size: 24px;
                width: 50px;
                height: 50px;
                line-height: 50px;
            }
        }
    }
    ul{
         margin: 0 auto 20px;
     
        li{
         font-size: 14px;
         &::marker{
             color: $primary;
         }
        }
    }
    .otp-img-wrap{
        max-width: 180px;
        margin: 0 auto 30px;
    } 
   }


