.stepwizard-wrapper{
    .stepwizard{
        
        @include media-breakpoint-up(md) {
            display: flex;
        }
        .stepwizard-panel{
            flex-shrink: 0;
            padding-right: 20px;
            
            @include media-breakpoint-up(md) {
                width: 200px;
            }
        }
        .stepwizard-content-wrapper{
            padding: 20px;
            border: 1px solid #ddd;
            margin-top: 20px;
            @include media-breakpoint-up(md) {
                width: calc(100% - 200px);
                min-height: 450px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                flex: 1;
                margin-top: 0;
            }
        }
    }
}